import React, { useState } from "react";
import styles from "./SelectCountry.module.scss";

const SelectCountry = ({ currentCountryCode, options, setCurrentCountryCode }) => {
  const [show, setShow] = useState(false);
  return (
    <div className={styles.select}>
      <div className={`${styles.selectMain} ${show ? styles.show : ""}`} onClick={() => setShow(!show)}>
        <span>{currentCountryCode.label}</span>
      </div>
      <div className={`${styles.selectMenu} ${show ? styles.show : ""}`}>
        {options.map((item) => {
          return (
            <div
              className={styles.selectMenu_item}
              key={item.code}
              onClick={() => {
                setCurrentCountryCode(item);
                setShow(!show);
              }}
            >
              {item.label}
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default SelectCountry;
