import React, { useState, useRef, useEffect } from "react";
import styles from "./Button.module.scss";
import LottieWeb from "lottie-web";
import Lottie from "react-lottie";
import loadingData from "../../../assets/flixxo-loading-button.json";
import ButtonData from "../../../assets/flixxo-watch-now-button.json";
import { useMediaQuery } from "react-responsive";
import ButtonPurchase from "../ButtonPurchase/ButtonPurchase";
const defaultOptions = {
  loop: true,
  autoplay: true,
};
const Button = ({
  fromLanding,
  type,
  children,
  disabled,
  isLoggedIn,
  canPurchased,
  onClick,
  isPrimary,
  fromNextEpisode,
  isSecondary,
  changeStatus,
  animation,
  customStyle,
  isLoading,
  omitir,
  ...props
}) => {
  const lottieLogoRef = useRef(null);
  const [lottieLogoAnimation, setLottieLogoAnimation] = useState(null);
  const [animationPlaying, setAnimationPlaying] = useState(false);
  const isMobile = useMediaQuery({ query: "(max-width: 600px)" });
  useEffect(() => {
    if (lottieLogoRef && !lottieLogoAnimation && canPurchased) {
      let lottieAnimation = LottieWeb.loadAnimation({
        container: lottieLogoRef.current,
        renderer: "svg",
        loop: false,
        autoplay: true,
        animationData: ButtonData,
        rendererSettings: {
          preserveAspectRatio: "xMidYMid slice",
          viewBoxOnly: true,
          className: styles.svg,
        },
      });
      lottieAnimation.setSpeed(1.5);
      setLottieLogoAnimation(lottieAnimation);
    }

    if (lottieLogoRef && lottieLogoAnimation) {
      if (changeStatus && !animationPlaying) {
        lottieLogoAnimation.goToAndStop(149, true);
      }
      if (!changeStatus && !animationPlaying) {
        lottieLogoAnimation.goToAndStop(0, true);
      }
    }
  }, [lottieLogoRef, lottieLogoAnimation, changeStatus, isLoading, canPurchased]);

  const clickHandler = () => {
    if (!changeStatus) {
      setAnimationPlaying(true);
      lottieLogoAnimation.playSegments([0, 149], true);
      lottieLogoAnimation.addEventListener("complete", () => {
        setAnimationPlaying(false);
      });
    }
  };
  // <ButtonPurchase />
  let check = lottieLogoAnimation && "wrapper" in lottieLogoAnimation;

  return (
    <>
      {fromLanding && !fromNextEpisode && !isMobile && canPurchased ? (
        <div
          className={styles.lottie}
          ref={lottieLogoRef}
          onClick={() => {
            onClick();
            clickHandler();
          }}
        ></div>
      ) : (
        <button
          type={type || "button"}
          className={`${styles.buttonModalAlert} ${fromNextEpisode ? styles.fromNextEpisode : ""} ${fromLanding ? styles.fromLanding : ""}${animation || ""}  ${customStyle || ""} ${
            changeStatus ? styles.changeStatus : ""
          } ${omitir ? styles.omitir : ""}`}
          disabled={disabled}
          onClick={onClick}
        >
          {isLoading ? <Lottie options={{ animationData: loadingData, ...defaultOptions }} width={50} speed={1} /> : children}
        </button>
      )}
    </>
  );
};

export default Button;
