import React, { useState } from "react";
import styles from "../ModalAlert.module.scss";
import PlayAsAnon from "./PlayAsAnon/PlayAsAnon";
import Register from "./Register/Register";
import SignIn from "./SignIn/SignIn";
import Recover from "./Recover/Recover";
import Validate from "./Validate/Validate";
import MobileNumber from "./MobileNumber/MobileNumber";
export const ModalAlertContent = ({ type, setTypeContent }) => {
  const [inputType, setinputType] = useState("password");

  const contentHandler = () => {
    switch (type) {
      case "playAsAnon":
        return <PlayAsAnon styles={styles} setTypeContent={setTypeContent} />;
      case "register":
        return <Register styles={styles} setTypeContent={setTypeContent} />;
      case "signIn":
        return <SignIn styles={styles} setTypeContent={setTypeContent} setinputType={setinputType} inputType={inputType} />;
      case "recover":
        return <Recover styles={styles} setTypeContent={setTypeContent} />;
      case "validate":
        return <Validate />;
      case "mobile":
        return <MobileNumber />;
      default:
        return null;
    }
  };

  return <section className={`${styles.modalAlert__contentSection} ${styles[type]}`}>{contentHandler()}</section>;
};

export default ModalAlertContent;
