import AdService from "../services/ad";
import * as types from "../actions/ActionTypes";
import { store } from "../storage/store";

export const get = () => {
  const secret = store.getState().authReducer.secret;
  const adService = new AdService(secret);

  return {
    type: types.AD_GET,
    payload: adService["get"](),
  };
};

export const post = (param) => {
  const secret = store.getState().authReducer.secret;
  const adService = new AdService(secret);

  return {
    type: types.AD_POST,
    payload: adService["post"](param),
  };
};

export const reward = () => {
  const secret = store.getState().authReducer.secret;
  const adService = new AdService(secret);

  return {
    type: types.AD_REWARD,
    payload: adService["reward"](),
  };
};

export const toggleAdModal = () => {
  return {
    type: types.AD_TOGGLE,
  };
};

export const reset = () => {
  return {
    type: types.AD_RESET,
  };
};
