import React from "react";
import Lottie from "react-lottie";
import loadingData from "../../../assets/flixxo-loading-button.json";
import styles from "./ButtonForm.module.scss";
import PropTypes from "prop-types";

const defaultOptions = {
  loop: true,
  autoplay: true,
};

const ButtonForm = ({
  type,
  disabled,
  onClick,
  isLoading,
  children,
  customStyle,
  size,
  hasHover,
  hasPlay,
  hasInfo,
  colorType,
  ...props
}) => {
  return (
    <button
      type={type}
      className={`${styles.buttonForm} ${customStyle || ""}  ${size === "small" &&
        styles.small} ${hasPlay && styles.play}  ${hasInfo && styles.info} ${
        styles[colorType]
      } ${hasHover && styles.hasHover}`}
      disabled={disabled}
      onClick={onClick}
      {...props}
    >
      {isLoading ? (
        <Lottie options={{ animationData: loadingData, ...defaultOptions }} width={50} speed={1} />
      ) : (
        children
      )}
    </button>
  );
};

ButtonForm.defaultProps = {
  type: "button",
  disabled: false,
  isLoading: false,
  size: "normal",
  hasPlay: false,
};

ButtonForm.propTypes = {
  type: PropTypes.string,
  disabled: PropTypes.bool,
  onClick: PropTypes.func,
  isLoading: PropTypes.bool,
  customStyle: PropTypes.object,
  children: PropTypes.element,
  size: PropTypes.oneOf(["small", "normal"]),
  colorType: PropTypes.oneOf(["", "purple", "gray"]),
  hasPlay: PropTypes.bool,
  hasInfo: PropTypes.bool,
  hasHover: PropTypes.bool,
};

export default ButtonForm;
