import { store } from "../../store/storage/store";
import { strings_en } from "./strings-en";
import { strings_es } from "./strings-es";

export const languages = [
  { label: "English", value: "en" },
  { label: "Spanish", value: "es" },
];

export function getString(key) {
  let langCode = "default";
  try {
    langCode = store.getState().userReducer.user.profile.lang;
  } catch (exception) {}

  const languages = {
    default: strings_en,
    en: strings_en,
    es: strings_es,
  };

  return langCode && languages[langCode] && languages[langCode][key] ? languages[langCode][key] : languages.default[key];
}
