import React, { useState, useEffect } from "react";
import { COUNTRY_CODES } from "../../../../utils/countryCodes";
import styles from "../RegisterForm/RegisterForm.module.scss";
import Button from "../../../UI/ButtonForm/ButtonForm";
import SelectCountry from "../../../UI/SelectCountry/SelectCountry";
import { setMobileNumber } from "../../../../store/actions/user";
import { showAlertModal } from "../../../../store/actions/auth";
import { connect } from "react-redux";
import GoBack from "../../../UI/GoBack/GoBack";

const MobileNumber = ({ setMobileNumber, showAlertModal, user }) => {
  let initialCountryCode = COUNTRY_CODES.find((item) => item.value === "+54");
  const [mobile, setMobile] = useState("");
  const [currentCountryCode, setCurrentCountryCode] = useState(initialCountryCode);
  const MOBILE_REGEX = /^\+?[1-9]\d{1,14}$/;

  const submitHandler = () => {
    const sendNumber = `${currentCountryCode.value}${mobile}`;
    let checkSendNumber = MOBILE_REGEX.test(sendNumber);
    if (checkSendNumber) {
      setMobileNumber({
        mobileNumber: sendNumber,
      }).then((res) => {
        if (res.value.success) {
          showAlertModal("validate");
        }
      });
    }
  };
  return (
    <>
      {user.profile?.mobileNumber ? (
        <GoBack fromModal fromModalHandler={() => showAlertModal("validate")} />
      ) : null}
      <h2 className={styles.subtitle}>
        Flixxo es una billetera que maneja criptomonedas. Es fundamental por tu seguridad validar tu
        cuenta con tu numero de teléfono.
      </h2>

      <form
        className={styles.form}
        onSubmit={(e) => {
          e.preventDefault();
          submitHandler();
        }}
      >
        <SelectCountry
          options={COUNTRY_CODES}
          currentCountryCode={currentCountryCode}
          setCurrentCountryCode={setCurrentCountryCode}
        />
        <div className={styles.registerForm__recoverInputContainer}>
          <input
            value={currentCountryCode.value}
            type='text'
            className={`${styles.registerForm__input} ${styles.registerForm__inputCountryCode}`}
            readOnly
          />
          <input
            value={mobile}
            type='text'
            placeholder='Ingresa tu numero de telefono'
            name='phone'
            id='phone'
            className={`${styles.registerForm__input}  ${styles.registerForm__inputRecover}`}
            onChange={(e) => setMobile(e.target.value)}
          />
        </div>

        <Button type={"submit"} customStyle={styles.btn} hasHover hasPlay>
          VALIDAR CUENTA
        </Button>
      </form>
    </>
  );
};

const mapStateToProps = (state) => ({
  user: state.userReducer.user,
});

export default connect(mapStateToProps, { setMobileNumber, showAlertModal })(MobileNumber);
