import React, { useState } from "react";
import Button from "../../../UI/ButtonForm/ButtonForm";
import styles from "../../ModalAlert.module.scss";
import { connect } from "react-redux";
import { useForm } from "react-hook-form";
import ResendCode from "../../../UI/ResendCode/ResendCode";
import { errorStringHandler } from "../../../../utils/ErrorStrings";

import {
  confirm,
  closeAlertModal,
  resendCode,
  openSuccessModal,
  showAlertModal,
} from "../../../../store/actions/auth";

const Validate = ({
  confirm,
  email,
  closeAlertModal,
  user,
  resendCode,
  openSuccessModal,
  showAlertModal,
}) => {
  const { register, handleSubmit, errors, setError, setValue, clearErrors } = useForm({
    mode: "onChange",
  });

  let pattern = /^\d+$/;

  let sendEmail = email || user.email;
  let { mobileNumber } = user.profile;

  const onSubmit = (data) => {
    let { code } = data;

    confirm({
      email: sendEmail,
      code: code,
    })
      .then((response) => {
        closeAlertModal();
        openSuccessModal();
      })
      .catch((error) => {
        setError("code", {
          type: "manual",
          message: error.error.message,
        });
      });
  };
  return (
    <>
      <div className={styles.badNumber} onClick={() => showAlertModal("mobile")}>
        ¿Ingresaste un numero incorrecto?
      </div>
      <form onSubmit={handleSubmit(onSubmit)}>
        <div
          className={`${styles.modalAlert__inputContainer} ${styles.modalAlert__inputContainer__code}`}
        >
          <input
            // value={code}
            type='text'
            placeholder='Ingresa aqui el Codigo'
            name='code'
            className={styles.modalAlert__input}
            ref={register({ required: "Required", pattern: pattern, minLength: 6, maxLength: 6 })}
            onChange={(e) => {
              if (errors?.code) {
                clearErrors("code");
              }
              // setValue("code", e.target.value);
            }}
            autoComplete='off'
          />
        </div>
        <p className={styles.error} style={{ marginTop: "-10px" }}>
          {errors.code?.type === "manual" && errorStringHandler(errors.code?.message)}
        </p>
        <ResendCode onClick={() => resendCode({ email: mobileNumber })} />
        <Button type={"submit"} customStyle={styles.sign} hasHover hasPlay>
          VALIDAR CUENTA
        </Button>
      </form>
    </>
  );
};

const mapStateToProps = (state) => ({
  user: state.userReducer.user,
  email: state.authReducer.email,
});

export default connect(mapStateToProps, {
  confirm,
  closeAlertModal,
  resendCode,
  openSuccessModal,
  showAlertModal,
})(Validate);
