import _ from "lodash";
import { flixxos } from "./constants";

export const parseBalance = (balance) => {
  let amount = 0;
  let prevAmount = 0;
  if (!_.isEmpty(balance)) {
    amount = flixxos(balance.amount);
    prevAmount = flixxos(balance.prevAmount);
  }
  return { amount, prevAmount };
};

export const canPurchase = (amount, price) => {
  return parseFloat(amount) > parseFloat(price);
};

export const findInPurchased = (contentPurchasedArray, contentToFind) => {
  return contentPurchasedArray.find((cont) => {
    return cont.uuid === contentToFind.uuid;
  });
};

export const findFirstNotPurchased = (contentPurchasedArray, contentArray) => {
  let episodeNumber;
  const firstNotPurchased = contentArray.find((item, index) => {
    const purchasedIndex = contentPurchasedArray.findIndex((purchased) => {
      return item.uuid === purchased.uuid;
    });
    episodeNumber = index + 1;
    return purchasedIndex === -1 && item;
  });
  if (firstNotPurchased) {
    return [firstNotPurchased, episodeNumber];
  } else {
    return [contentArray[0], 1];
  }
};

export const parseSubtitles = (subtitles) => subtitles.map((sub) => sub.lang).join();
