import * as types from "../actions/ActionTypes";

const defaultState = {
  showSearch: false,
  searchText: "",
  searchFilter: false,
  searchPage: 1,
  searchKey: "",
  searchValue: "",
  isSearching: false,
  searchResults: [],
};

export default function reducer(state = defaultState, action) {
  switch (action.type) {
    case types.SEARCH_SHOW:
      return {
        ...state,
        isSearching: true,
      };
    case types.SEARCH_HIDE:
      return {
        ...state,
        isSearching: false,
        searchText: "",
      };
    case types.SEARCH_TEXT:
      return Object.assign({}, state, {
        searchText: action.payload,
      });
    case types.SEARCH_FILTER:
      return Object.assign({}, state, {
        searchFilter: action.payload,
      });
    case types.SEARCH_PAGE:
      return Object.assign({}, state, {
        searchPage: action.payload,
      });
    case types.SEARCH_KEY:
      return Object.assign({}, state, {
        searchKey: action.payload,
      });
    case types.SEARCH_VALUE:
      return Object.assign({}, state, {
        searchValue: action.payload,
      });
    case types.SEARCH_PENDING:
      return Object.assign({}, state, {
        // isSearching: true,
        searchResults: [],
      });
    case types.SEARCH_FULFILLED:
      return Object.assign({}, state, {
        // isSearching: false,
        searchResults: action.payload,
      });
    case types.SEARCH_REJECTED:
      return Object.assign({}, state, {
        // isSearching: false,
        searchResults: [],
      });
    default:
      return state;
  }
}
