import React, { useState, useEffect } from "react";
import styles from "./CarouselItem.module.scss";
import ContentInfo from "../../UI/ContentInfo/ContentInfo";
import ContentActions from "../../UI/ContentActions/ContentActions";
import { Slide } from "pure-react-carousel";
import { getMainCover } from "../../../utils/thumbsManager";
import "pure-react-carousel/dist/react-carousel.es.css";
import { useMediaQuery } from "react-responsive";
import { TYPE_SERIE } from "../../../utils/constants";
import { canPurchase, findInPurchased } from "../../../utils/common";
import useButtonHandler from "../../../utils/hooks/UseButtonHandler";
import { motion } from "framer-motion";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { useHistory } from "react-router-dom";

const variants = {
  in: {
    y: "14vw",
    opacity: 1,
    transition: {
      delay: 0.4,
      duration: 0.4,
      ease: "easeOut",
    },
  },
  out: {
    y: "20vw",
    opacity: 0,
    transition: {
      delay: 0.4,
      duration: 0.4,
      ease: "easeOut",
    },
  },
};

const variants2 = {
  enter: {
    x: "0",
    opacity: 1,
    transition: {
      delay: 0.4,
      duration: 0.4,
      ease: "easeOut",
    },
  },
  exit: {
    x: "-200px",
    opacity: 0,
    transition: {
      delay: 0.4,
      duration: 0.4,
      ease: "easeOut",
    },
  },
};

const CarouselItem = ({
  video,
  index,
  fromLanding,
  fromHome,
  isEpisode,
  isLoggedIn,
  balance,
  contentPurchasedArray,
  communityFilter,
}) => {
  const isMobile = useMediaQuery({ query: "(max-width: 600px)" });
  const [isHovered, setIsHovered] = useState(false);
  const history = useHistory();
  const isSerie = video.contentType === TYPE_SERIE;

  const getMainThumbUrl = () => {
    const type = isMobile ? "xcover" : "hero";
    const mainThumbUrl = isSerie ? video.media[0].sizes[type] : getMainCover(video, type);
    return mainThumbUrl;
  };
  const mainThumbUrl = getMainThumbUrl();

  const canPurchased = canPurchase(balance.amount, video.price);
  let isPurchased = findInPurchased(contentPurchasedArray, video);
  const contentType = video.contentType === 2 ? "series" : "community";

  const [buttonHandler] = useButtonHandler(
    isPurchased,
    canPurchased,
    fromHome,
    false,
    video,
    contentType
  );

  const secondaryHandler = () => {
    history.push(`/${communityFilter}/${isLoggedIn && isSerie ? video.serieUuid : video.uuid}`);
  };

  return (
    <Slide index={index} classNameVisible={styles.visible} classNameHidden={styles.hidden}>
      <div
        className={`${styles.carousel}`}
        onPointerEnter={() => setIsHovered(true)}
        onPointerLeave={() => setIsHovered(false)}
      >
        {!isMobile && (
          <>
            {isSerie ? (
              <motion.div
                className={styles.contentInfo}
                variants={variants}
                initial='out'
                animate={isHovered ? "in" : "out"}
                key='motiontest'
              >
                <ContentActions
                  contentType={contentType}
                  canPurchase={canPurchased}
                  isPurchased={isPurchased}
                  isLoggedIn={isLoggedIn}
                  price={video.price}
                  episodeNumber={video.episodeNumber}
                  primaryButtonHandler={buttonHandler}
                  secondaryButtonHandler={secondaryHandler}
                />
              </motion.div>
            ) : (
              <motion.div
                className={styles.contentInfo}
                variants={variants2}
                initial='out'
                animate='enter'
                key='doble'
              >
                <ContentInfo
                  video={video}
                  fromLanding={fromLanding}
                  fromHome={fromHome}
                  isEpisode={isEpisode}
                />
              </motion.div>
            )}
          </>
        )}

        <div className={`${styles.carouselBackground}`} onClick={secondaryHandler}>
          {!isSerie && (
            <figure className={`${styles.carouselLeftGradient} ${isHovered && styles.isHovered}`} />
          )}
          <figure className={styles.carouselTopGradient} />
          <img src={mainThumbUrl} alt='test' />
          <figure
            className={`${styles.carouselBottomGradient} ${isHovered &&
              isSerie &&
              styles.isHovered}`}
          />
        </div>
      </div>
    </Slide>
  );
};

CarouselItem.propTypes = {
  video: PropTypes.object,
  index: PropTypes.number,
  fromLanding: PropTypes.bool,
  fromHome: PropTypes.bool,
  isEpisode: PropTypes.bool,
};

const mapStateToProps = (state) => {
  return {
    allCategories: state.categorieReducer.categories,
    isLoggedIn: state.authReducer.isLoggedIn,
    balance: state.userReducer.balance,
    communityFilter: state.userReducer.communityFilter,
    currentSerieLanding: state.contentReducer.currentSerieLanding,
    contentPurchasedArray: state.userReducer.contentPurchased,
    paymentContentPosting: state.paymentReducer.paymentContentPosting,
  };
};

export default connect(mapStateToProps)(CarouselItem);
