import React, { useState, useEffect } from "react";
import styles from "./NextEpisode.module.scss";
import { FLIXX_DECIMALS, TYPE_SERIE, flixxos } from "../../../utils/constants";
import {
  getTileCoverUrl,
  getSwitchingThumbUrl,
  getSwitchingThumbUrlSerie,
} from "../../../utils/thumbsManager";
import { connect } from "react-redux";
import { getWebLink, setContentWeblinkURL } from "../../../store/actions/content";
import {
  setShowNextEpisode,
  setDismissNextEpisode,
  setPlayerEnd,
} from "../../../store/actions/media";
import {
  contentPurchased as SetContentPurchased,
  balance as userBalance,
} from "../../../store/actions/user";
import { content as buyContent } from "../../../store/actions/payment";
import ContenInfo from "../ContentInfo/ContentInfo";

const NextEpisode = ({
  currentSerieLanding,
  currentContentLanding,
  balance,
  dismissNextEpisode,
  setDismissNextEpisode,
  showNextEpisode,
  setShowNextEpisode,
  currentSeason,
  playerDuration,
  playerProgress,
  contentPurchased,
}) => {
  const [isShowing, setIsShowing] = useState(false);
  const [cancelNextEpisode, setCancelNextEpisode] = useState(false);
  const [seconds, setSeconds] = useState(10);
  const [check, setCheck] = useState(null);

  useEffect(() => {
    if (playerDuration && playerProgress) {
      const check = playerProgress.playedSeconds > playerDuration - 15;
      if (check && !showNextEpisode && !dismissNextEpisode) {
        setShowNextEpisode();
      }
      // else if (cancelNextEpisode) {
      //   setIsShowing(false);
      // }
    }
    return () => {};
  }, [
    playerProgress,
    playerDuration,
    cancelNextEpisode,
    showNextEpisode,
    setShowNextEpisode,
    dismissNextEpisode,
  ]);

  useEffect(() => {
    if (showNextEpisode && !dismissNextEpisode) {
      let interval = setInterval(function() {
        if (seconds > 0) setSeconds(seconds - 1);
      }, 1000);

      if (seconds === 1 || dismissNextEpisode) {
        clearInterval(interval);
        interval = null;
        setDismissNextEpisode();
        // setCancelNextEpisode(true);
      }
      return () => clearInterval(interval);
    }
  }, [seconds, showNextEpisode, setDismissNextEpisode, dismissNextEpisode]);

  let currentEpi = currentSerieLanding.season[currentSeason].content.findIndex((cont, index) => {
    return currentContentLanding.uuid === cont.uuid;
  });
  let nextEpisode = currentSerieLanding.season[currentSeason].content[currentEpi + 1];
  let nextEpisodeNumber = currentEpi + 2;

  if (nextEpisodeNumber < 10) {
    nextEpisodeNumber = `0${nextEpisodeNumber}`;
  }

  const getMainThumbUrl = () => {
    const mainThumbUrl =
      nextEpisode.contentType === TYPE_SERIE || nextEpisode.isSerieEpisode === true
        ? getSwitchingThumbUrlSerie(nextEpisode, "cover", 0, true)
        : getTileCoverUrl(nextEpisode);
    return mainThumbUrl;
  };

  const findInPurchased = (contentToFind) => {
    return contentPurchased.find((cont) => {
      return cont.uuid === contentToFind.uuid;
    });
  };

  let mainThumbUrl;
  let isPurchased;
  let canPurchase;

  if (nextEpisode) {
    mainThumbUrl = getMainThumbUrl();
    isPurchased = findInPurchased(nextEpisode);
    canPurchase = parseFloat(balance.amount) > parseFloat(nextEpisode.price);
  }

  return nextEpisode && (canPurchase || isPurchased) ? (
    <section className={`${styles.nextEpisode} ${showNextEpisode ? styles.show : ""}`}>
      <div className={styles.nextEpisode_caratula}>
        <img src={mainThumbUrl} alt='' />
      </div>
      <div>
        <h4 className={styles.remaining}>
          El próximo episodio se omite en <span>{seconds} segundos</span>
        </h4>
        <ContenInfo
          video={nextEpisode}
          nextEpisodeNumber={nextEpisodeNumber}
          isEpisode={true}
          fromNextEpisode={true}
        />
      </div>
    </section>
  ) : null;
};

const mapStateToProps = (state) => {
  return {
    playerProgress: state.mediaReducer.playerProgress,
    playerDuration: state.mediaReducer.playerDuration,
    playerHasEnd: state.mediaReducer.playerHasEnd,
    prevPlayerProgress: state.mediaReducer.prevPlayerProgress,
    showNextEpisode: state.mediaReducer.showNextEpisode,
    dismissNextEpisode: state.mediaReducer.dismissNextEpisode,
    bufferEnd: state.mediaReducer.bufferEnd,
    contentPurchased: state.userReducer.contentPurchased,
    communityFilter: state.userReducer.communityFilter,
    balance: state.userReducer.balance,
  };
};
export default connect(mapStateToProps, {
  getWebLink,
  setContentWeblinkURL,
  userBalance,
  setPlayerEnd,
  setDismissNextEpisode,
  setShowNextEpisode,
  SetContentPurchased,
  buyContent,
})(NextEpisode);
