import React, { useEffect } from "react";
import ErrorHandler from "./components/Error/ErrorHandler";
import logo from "./logo.svg";
import "./App.css";
import ModalAlert from "./components/ModalAlert/ModalAlert";
import ModalSuccess from "./components/UI/ModalSuccess/ModalSuccess";
import Nav from "./components/Nav/Nav";
import Footer from "./components/Footer/Footer";
import { Route, Switch, Redirect, withRouter } from "react-router-dom";
import { test } from "./store/actions/content";
import Home from "./pages/Home/Home";
import ContentLanding from "./pages/VideoLanding/ContentLanding";
import NotFound from "./pages/NotFound/NotFound";
import { connect } from "react-redux";
import Loading from "./components/UI/Loading/Loading";
import ModalAd from "./components/UI/ModalAd/ModalAd";
import { getFolloweds } from "./store/actions/follow";
import Search from "./pages/Search/Search";
import { GAHandler } from "./utils/GA";
import RedirectPage from "./pages/Redirect/Redirect";
import { reward as getCurrentReward } from "./store/actions/ad";
import {
  COMMUNITY_FILTER,
  CONTENT_NUMBER_FILTER,
  STATE_ENABLED,
  STATE_CONFIRMED,
  STATE_SMS_CONFIRMED,
} from "./utils/constants";
import {
  me as onGetUserMe,
  balance as userBalance,
  followedCategoriesGet,
  contentPurchased,
  toggleAllowExitUpload,
  reset as userReset,
  changeCommunityFilter,
} from "./store/actions/user";
import {
  logout,
  initClient,
  showAlertModal as showAlertModalAction,
  closeAlertModal,
  loginExternal,
} from "./store/actions/auth";
const App = ({
  isLoggedIn,
  showAlertModal,
  showAlertModalType,
  adOpen,
  secret,
  onGetUserMe,
  initClient,
  userBalance,
  contentPurchased,
  closeAlertModal,
  getFolloweds,
  getCurrentReward,
  showAlertModalAction,
  showSuccessModal,
  loginExternal,
}) => {
  useEffect(() => {
    //Listen for authorization success
    document.addEventListener("AppleIDSignInOnSuccess", (data) => {
      let code = data.detail.authorization.code;

      loginExternal("apple", code)
        .then(() => {
          // history.push("/");
          GAHandler("Iniciar Sesión", "IniciarSesión_Exitosa");
        })
        .catch(() => {
          // history.push("/");
          GAHandler("Iniciar Sesión", "IniciarSesión_Erronea");
        });
    });
    //Listen for authorization failures
    document.addEventListener("AppleIDSignInOnFailure", (error) => {});
  }, []);

  useEffect(() => {
    if (isLoggedIn) {
      getUserMe();
    }
  }, [isLoggedIn]);

  const getUserMe = () => {
    if (secret) {
      return onGetUserMe().then(getUserMeSuccess.bind(this));
    }
  };

  const getUserMeSuccess = (response) => {
    let { status, profile } = response.value;

    if (!(status & STATE_CONFIRMED)) {
      if (!profile.mobileNumber) {
        // this.context.router.history.push("/old-user");
        showAlertModalAction("mobile");
      } else {
        showAlertModalAction("validate");
        //Para usuarios no confirmados pero nuevos (pantalla confirmar sms)
        // this.context.router.history.push("/mainScreen");
      }
    } else {
      Promise.all([
        initClient(),
        userBalance(),
        followedCategoriesGet(),
        contentPurchased(COMMUNITY_FILTER.COMMUNITY),
        contentPurchased(COMMUNITY_FILTER.SERIES),
      ]);
      closeAlertModal();
    }
  };
  return (
    <div>
      <ErrorHandler>
        <Nav isLoggedIn={isLoggedIn} />
        <Switch>
          <Route exact path='/' component={Home} />
          <Route exact path='/community/:id' component={ContentLanding} />
          <Route exact path='/series/:id' component={ContentLanding} />
          <Route exact path='/redirect' component={RedirectPage} />
          <Route path='/search' component={Search} />
          <Route component={NotFound} />
        </Switch>
        <Footer />
        {showAlertModal ? <ModalAlert /> : null}
        {showSuccessModal ? <ModalSuccess /> : null}
        {adOpen ? <ModalAd /> : null}
      </ErrorHandler>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    isLoggedIn: state.authReducer.isLoggedIn,
    showAlertModal: state.authReducer.showAlertModal,
    showSuccessModal: state.authReducer.showSuccessModal,
    showAlertModalType: state.authReducer.showAlertModalType,
    adOpen: state.adReducer.adOpen,
    secret: state.authReducer.secret,
  };
};

export default connect(mapStateToProps, {
  onGetUserMe,
  showAlertModalAction,
  initClient,
  userBalance,
  loginExternal,
  closeAlertModal,
  contentPurchased,
  getFolloweds,
  getCurrentReward,
})(App);
