import React, { useEffect } from "react";
import { connect } from "react-redux";
import { loginExternal } from "../../store/actions/auth";
import { GAHandler } from "../../utils/GA";
import styles from "./Redirect.module.scss";
const Redirect = ({ history, location, match, loginExternal }) => {
  useEffect(() => {
    let code = location.search?.split("?code=")[1];
    const loginApple = () => {
      loginExternal("apple", code)
        .then(() => {
          history.push("/");
          GAHandler("Iniciar Sesión", "IniciarSesión_Exitosa");
        })
        .catch(() => {
          history.push("/");
          GAHandler("Iniciar Sesión", "IniciarSesión_Erronea");
        });
    };
    if (code) loginApple(code);
    else history.push("/");
  }, []);

  return <main className={styles.redirect}></main>;
};

export default connect(null, { loginExternal })(Redirect);
