import React, { useState, useEffect } from "react";
import styles from "./SerieSliderWrapper.module.scss";
import { CarouselProvider, Slider, ButtonBack, ButtonNext } from "pure-react-carousel";
import "pure-react-carousel/dist/react-carousel.es.css";
import { ReactComponent as ArrowLeft } from "../../../assets/arrow-left.svg";
import { ReactComponent as ArrowRight } from "../../../assets/arrow-right.svg";
import { useSelector } from "react-redux";
import { useMediaQuery } from "react-responsive";
import useMediaValues from "../../../utils/hooks/useMediaValues";

const useMediaConfig = {
  mobile: 2,
  tab: 2,
  tabLand: 3,
  desktop: 4,
  bigDesktop: 5,
  defaultValue: 6,
};

const SliderWrapper = ({ children, videos, length }) => {
  const [initialEpisode, setInitialEpisode] = useState(0);
  const isMobile = useMediaQuery({ query: "(max-width: 600px)" });
  const currentContentLanding = useSelector((state) => state.contentReducer.currentContentLanding);
  useEffect(() => {
    setInitialEpisode(currentContentLanding.episodeNumber - 1 || 0);
  }, []);

  const visibleSlide = useMediaValues(useMediaConfig);

  let lengthVisible = length < 5 ? length : 5;
  return (
    <div className={styles.SliderWrapper}>
      <CarouselProvider
        orientation={isMobile ? "vertical" : "horizontal"}
        isIntrinsicHeight={isMobile ? false : true}
        naturalSlideWidth={245}
        naturalSlideHeight={isMobile ? 322 : 400}
        totalSlides={length}
        step={isMobile ? lengthVisible : visibleSlide}
        visibleSlides={isMobile ? lengthVisible : visibleSlide}
        dragEnabled={false}
        dragStep={false}
        infinite={true}
        lockOnWindowScroll={true}
        currentSlide={initialEpisode}
      >
        <Slider>{children}</Slider>
        <>
          <ButtonBack className={`${styles.button} ${styles.back}`}>
            <ArrowLeft />
          </ButtonBack>
          <ButtonNext className={`${styles.button} ${styles.next}`}>
            <ArrowRight style={{ opacity: 1 }} />
          </ButtonNext>{" "}
        </>
      </CarouselProvider>
    </div>
  );
};

export default SliderWrapper;
