import React, { useEffect } from "react";
import { useForm } from "react-hook-form";
import styles from "./RegisterForm.module.scss";
import Button from "../../../UI/ButtonForm/ButtonForm";
import { errorStringHandler } from "../../../../utils/ErrorStrings.js";
const Step1 = ({ setStep, setFormData, formData, errorFromAPI, authCheck }) => {
  const { handleSubmit, register, errors, setError, watch, clearErrors, formState } = useForm({
    defaultValues: { ...formData, password: "", confirmPassword: "" },
    mode: "onChange",
  });

  useEffect(() => {
    if (errorFromAPI) {
      setError(errorFromAPI.type, {
        type: "manual",
        message: errorFromAPI.message,
      });
    }
  }, [errorFromAPI]);

  const passPattern = /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9]).{8,}$/;

  const onSubmit = async (values) => {
    const body = {
      email: values.email,
      nickname: values.nickname,
    };

    const response = await authCheck(body);
    const validation = response.value.error?.details;

    if (response.value.success) {
      setStep((s) => s + 1);
      setFormData(values);
    } else if (validation) {
      validation.map((error) => {
        setError(error.field, {
          type: "manual",
          message: error.message,
        });
      });
    }
  };

  return (
    <>
      <form className={styles.form} onSubmit={handleSubmit(onSubmit)}>
        <div className={`${styles.inputContainer} ${styles.nickname}`}>
          <input
            placeholder='Nombre de usuario'
            name='nickname'
            ref={register({ required: "Required", minLength: 3, maxLength: 16 })}
            type='text'
            onChange={() => {
              if (errorFromAPI?.type === "nickname") {
                clearErrors("nickname");
              }
            }}
          />
        </div>
        <span className={styles.error}>
          {errors.nickname?.type === "required" && "Este campo es requerido"}
          {errors.nickname?.type === "manual" && errorStringHandler(errors.nickname?.message)}
          {(errors.nickname?.type === "minLength" || errors.nickname?.type === "maxLength") &&
            "El usuario debe tener entre 3 y 16 caracteres."}
        </span>
        <div className={`${styles.inputContainer} ${styles.email}`}>
          <input
            name='email'
            placeholder='Email'
            ref={register({ required: "Required" })}
            type='email'
            onChange={() => {
              if (errorFromAPI?.type === "email") {
                clearErrors("email");
              }
            }}
          />
        </div>
        <span className={styles.error}>
          {errors.email?.type === "required" && "Este campo es requerido"}
          {errors.email?.type === "manual" && errorStringHandler(errors.email?.message)}
        </span>
        <div className={`${styles.inputContainer} ${styles.password}`}>
          <input
            placeholder='Ingresa tu contraseña'
            name='password'
            ref={register({ required: "Required", pattern: passPattern })}
            type='password'
          />
        </div>
        <span className={styles.error}>
          {errors.password?.type === "required" && "Este campo es requerido"}
          {errors.password?.type === "pattern" &&
            "Debe contener al menos una letra mayuscula, un numero y 8 caracteres."}
        </span>
        <div className={`${styles.inputContainer} ${styles.password}`}>
          <input
            placeholder='Confirma tu contraseña'
            name='confirmPassword'
            ref={register({
              required: "Required",
              pattern: passPattern,
              validate: (value) => value === watch("password"),
            })}
            type='password'
          />
        </div>
        <span className={styles.error}>
          {errors.confirmPassword?.type === "required" && "Este campo es requerido"}
          {errors.confirmPassword?.type === "pattern" &&
            "Debe contener al menos una letra mayuscula, un numero y 8 caracteres."}
          {errors.confirmPassword?.type === "validate" && "La contraseña no coincide"}
        </span>
        <Button
          type={"submit"}
          customStyle={styles.btn}
          disabled={!formState.isValid}
          hasHover
          hasPlay
        >
          Empezá
        </Button>
      </form>
    </>
  );
};

export default Step1;
