import React, { useEffect, useState, useRef } from "react";
import styles from "./Nav.module.scss";
import { ReactComponent as FlixxoNavLogo } from "../../assets/flixxo-logo-nav.svg";
import Search from "./Search/Search";
import Switch from "./Switch/Switch";
import { connect } from "react-redux";
import { showAlertModal } from "../../store/actions/auth";
import { get as getAd, toggleAdModal } from "../../store/actions/ad";
import _ from "lodash";
import { Link, useHistory, useLocation } from "react-router-dom";
import SideNav from "../UI/SideNav/SideNav";
import logoData from "../../assets/flixxo-logo.json";
import burgerData from "../../assets/burger-lottie.json";
import { useMediaQuery } from "react-responsive";
import LottieWeb from "lottie-web";
import { ReactComponent as NavigateBack } from "../../assets/navigate-back.svg";
import { GAHandler } from "../../utils/GA";
import UserInfo from "../UI/UserInfo/UserInfo";
import Button from "../UI/ButtonForm/ButtonForm";
import PropTypes from "prop-types";

const Nav = ({
  showAlertModal,
  isLoggedIn,
  user,
  balance,
  toggleAdModal,
  isSearching,
  communityFilter,
}) => {
  const lottieLogoRef = useRef(null);
  const [lottieLogoAnimation, setLottieLogoAnimation] = useState(null);
  const history = useHistory();
  const location = useLocation();

  const lottieBurgerRef = useRef(null);
  const [burgerAnimation, setburgerAnimation] = useState(null);

  useEffect(() => {
    if (lottieLogoRef && !lottieLogoAnimation) {
      let lottieAnimation = LottieWeb.loadAnimation({
        container: lottieLogoRef.current,
        renderer: "svg",
        loop: false,
        autoplay: true,
        animationData: logoData,
        rendererSettings: {
          preserveAspectRatio: "xMidYMid slice",
          viewBoxOnly: true,
          className: styles.nav__actions_logo_lottie,
        },
      });
      setLottieLogoAnimation(lottieAnimation);
    }

    if (lottieBurgerRef && !burgerAnimation) {
      let lottieBurger = LottieWeb.loadAnimation({
        container: lottieBurgerRef.current,
        renderer: "svg",
        loop: false,
        autoplay: false,
        animationData: burgerData,
        rendererSettings: {
          preserveAspectRatio: "xMidYMid slice",
          viewBoxOnly: true,
          className: styles.burger,
        },
      });
      lottieBurger.setSpeed(2);
      setburgerAnimation(lottieBurger);
    }
  }, [lottieLogoRef, lottieLogoAnimation]);

  const isMobile = useMediaQuery({ query: "(max-width: 600px)" });

  const isTab = useMediaQuery({ query: "(max-width: 1100px)" });
  const [scroll, setScroll] = useState(false);
  const [showSideNav, showSideNavHandler] = useState(false);

  useEffect(() => {
    document.addEventListener("scroll", () => {
      const scrollCheck = window.scrollY > 10;
      if (scrollCheck !== scroll) {
        setScroll(scrollCheck);
      } else {
        setScroll(false);
      }
    });
  }, []);

  useEffect(() => {
    if (isMobile) window.scrollTo(0, 150);
  }, [isMobile, communityFilter, location]);

  const rewardsHandler = () => {
    GAHandler("Despliegue del Pop Up", "PopUp_ClickContenido");
    toggleAdModal();
  };

  const burgerMenuHandler = () => {
    if (showSideNav) burgerAnimation.playSegments([40, 0], true);
    else burgerAnimation.playSegments([0, 40], true);
    showSideNavHandler(!showSideNav);
  };

  const checkLocationMobile2 =
    isMobile &&
    (window.location.pathname.includes("series") || window.location.pathname.includes("community"));

  return (
    <>
      {!isMobile ? (
        <div
          ref={lottieBurgerRef}
          onClick={burgerMenuHandler}
          className={`${isSearching ? "styles.isSearching " : ""}`}
        />
      ) : null}
      <nav className={`${styles.nav} ${scroll ? styles.scrolled : ""}`}>
        <div className={styles.navContainer}>
          {checkLocationMobile2 ? (
            <NavigateBack
              onClick={() => history.push("/")}
              style={{
                width: "18px",
                height: "18px",
                marginRight: "20px",
                display: "flex",
                flex: "0 1 auto",
              }}
            />
          ) : null}
          <div className={`${styles.nav__actions} ${isMobile ? styles.isMobile : ""}`}>
            {!isMobile ? (
              <>
                <Link
                  to='/'
                  className={`${styles.nav__actions_logo} ${
                    isSearching ? "styles.isSearching" : ""
                  }`}
                  onMouseEnter={() => {
                    if (lottieLogoAnimation.currentFrame >= lottieLogoAnimation.totalFrames - 20) {
                      lottieLogoAnimation.playSegments([0, 90], true);
                    }
                  }}
                >
                  <div
                    ref={lottieLogoRef}
                    className={`${isSearching ? "styles.isSearching" : ""}`}
                  ></div>
                </Link>
                <Switch isSearching={isSearching} communityFilter={communityFilter} />
                <Search />
              </>
            ) : (
              <>
                <Link to='/' className={styles.nav__actions_logo}>
                  <FlixxoNavLogo style={{ width: "86px" }} />
                </Link>
                <Search isMobile />
              </>
            )}
          </div>
          {isMobile || isTab ? null : (
            <div className={styles.nav__winFlixx}>
              <Button
                size='small'
                onClick={rewardsHandler}
                colorType='purple'
                customStyle={`${styles.winFlixx} ${isSearching ? styles.isSearching : ""}`}
              >
                GANÁ FLIXX
              </Button>
            </div>
          )}
          {!isMobile && (
            <div className={styles.nav__user}>
              {isLoggedIn ? (
                <UserInfo balance={balance} avatar={user?.profile?.avatar} />
              ) : (
                <Button
                  size='small'
                  onClick={() => {
                    GAHandler("Despliegue del Pop Up", "PopUp_ClickLogin");
                    showAlertModal("playAsAnon");
                  }}
                >
                  INICIAR SESIÓN
                </Button>
              )}
            </div>
          )}
        </div>
        {isMobile &&
        !(
          window.location.pathname.includes("series") ||
          window.location.pathname.includes("community") ||
          window.location.pathname.includes("search")
        ) ? (
          <Switch isSearching={isSearching} communityFilter={communityFilter} />
        ) : null}
      </nav>
      <SideNav
        showSideNavHandler={showSideNavHandler}
        showSideNav={showSideNav}
        showAlertModal={showAlertModal}
        isLoggedIn={isLoggedIn}
      />
    </>
  );
};

Nav.propTypes = {
  user: PropTypes.shape({
    uuid: PropTypes.string,
    email: PropTypes.string,
    nickname: PropTypes.string,
    status: PropTypes.number,
    profile: PropTypes.shape({
      userId: PropTypes.string,
      realName: PropTypes.string,
      birthDate: PropTypes.string,
      lang: PropTypes.string,
      gender: PropTypes.oneOf([1, 2]),
      countryId: PropTypes.number,
      avatar: PropTypes.string,
      mobileNumber: PropTypes.string,
      inviter: PropTypes.string,
    }),
  }),
  balance: PropTypes.shape({
    amount: PropTypes.number,
    prevAmount: PropTypes.string,
  }),
};

const mapStateToProps = (state) => {
  return {
    user: state.userReducer.user,
    balance: state.userReducer.balance,
    adReward: state.adReducer.adReward,
    adGot: state.adReducer.adGot,
    isSearching: state.searchReducer.isSearching,
    communityFilter: state.userReducer.communityFilter,
  };
};

export default connect(mapStateToProps, { showAlertModal, getAd, toggleAdModal })(Nav);
