import React, { useState } from "react";
import LogoFacebook from "../../../../assets/icon-facebook.svg";
import LogoApple from "../../../../assets/icon-apple.svg";
import { login, setPreviousAlertModal } from "../../../../store/actions/auth";
import { connect } from "react-redux";
import { useHistory } from "react-router-dom";
import Button from "../../../UI/ButtonForm/ButtonForm";
import { GAHandler } from "../../../../utils/GA";
import { modalRedirect } from "../../../../utils/constants";
import { errorStringHandler } from "../../../../utils/ErrorStrings.js";
import GoBack from "../../../UI/GoBack/GoBack";

const defaultOptions = {
  loop: true,
  autoplay: true,
};

const SignIn = ({
  styles,
  inputType,
  setinputType,
  login,
  isLoging,
  setTypeContent,
  loginError,
  setPreviousAlertModal,
}) => {
  const [userName, setUserName] = useState("");
  const [password, setPassword] = useState("");

  const history = useHistory();
  const loginUser = (e) => {
    e.preventDefault();
    login(userName, password)
      .then(() => {
        GAHandler("Iniciar Sesión", "IniciarSesión_Exitosa");
      })
      .catch(() => {
        GAHandler("Iniciar Sesión", "IniciarSesión_Erronea");
      });
  };
  return (
    <>
      <GoBack fromModal fromModalHandler={() => setPreviousAlertModal(modalRedirect.playAsAnon)} />
      <form className={styles.form} onSubmit={loginUser}>
        <div className={styles.modalAlert__inputContainer}>
          <input
            type='text'
            placeholder='Nombre de Usuario'
            name='user'
            id='user'
            className={styles.modalAlert__input}
            onChange={(e) => setUserName(e.target.value)}
          />
        </div>

        <div
          className={`${styles.modalAlert__inputContainer} ${styles.modalAlert__inputContainer__password}`}
        >
          <input
            type={inputType}
            placeholder='Ingresá tu Contraseña'
            name='password'
            id='password'
            className={styles.modalAlert__input}
            onChange={(e) => setPassword(e.target.value)}
          />
          <figure
            className={`${styles.modalAlert__inputEye} ${inputType === "text" ? styles.hide : ""}`}
            onClick={() => setinputType(inputType === "password" ? "text" : "password")}
          />
        </div>
        {loginError ? (
          <p className={styles.error} style={{ marginTop: "10px" }}>
            {errorStringHandler(loginError.message)}
          </p>
        ) : null}

        <h3
          className={`${styles.modalAlert__subText} ${styles.recover}`}
          onClick={() => {
            GAHandler("Iniciar Sesión", "IniciarSesión_ClickRecuperarContraseña");
            setTypeContent("recover");
          }}
        >
          Recuperar <span>Contraseña</span>
        </h3>

        <Button type={"submit"} isLoading={isLoging} customStyle={styles.sign} hasHover hasPlay>
          Iniciar Sesión
        </Button>
        {/* 
      <h3 className={styles.modalAlert__subText}>
        Tambien podés<span> registrarte con:</span>
      </h3> */}

        {/* <div className={styles.modalAlert__registerContainer}>
        <figure className={styles.modalAlert__register}>
          <img src={LogoFacebook} alt='facebook logo' />
        </figure>
        <figure className={styles.modalAlert__register}>
          <img src={LogoApple} alt='apple logo' />
        </figure>
      </div> */}
      </form>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    isLoging: state.authReducer.isLoging,
    loginError: state.authReducer.loginError,
  };
};

export default connect(mapStateToProps, { login, setPreviousAlertModal })(SignIn);
