import React from "react";
import styles from "./NotFound.module.scss";
import { ReactComponent as OopsSVG } from "../../assets/oops.svg";
import NotFoundImage from "../../assets/404.png";
import Button from "../../components/UI/Button/Button";
const NotFound = ({ history }) => {
  return (
    <div className={styles.notfound}>
      <OopsSVG className={styles.oops} />
      <img src={NotFoundImage} alt='404' className={styles.error} />
      <h1>PAGINA NO ENCONTRADA</h1>
      <Button onClick={() => history.push("/")} customStyle={styles.notfound_button}>
        Volver al Inicio
      </Button>
      {/* <div className={styles.notfound_refresh} onClick={() => history.push("/")}>
        Volver al Inicio
      </div> */}
    </div>
  );
};

export default NotFound;
