import React from "react";
import styles from "./DurationAuthorInfo.module.scss";
import Avatar from "../Avatar/Avatar";
import { ReactComponent as VeryShortHand } from "../../../assets/icon-mano-muy-corta.svg";
import { ReactComponent as ShortHand } from "../../../assets/icon-mano-corta.svg";
import { ReactComponent as LongHand } from "../../../assets/icon-mano-larga.svg";
import DefaultProfilePic from "../../../assets/ProfilePic.svg";

const DurationAuthorInfo = ({
  minutes,
  author,
  episodeSelected,
  isEpisode,
  contentType,
  ...props
}) => {
  const durationIconHandler = (minutes) => {
    switch (true) {
      case minutes <= 5:
        return (
          <>
            <span className={styles.makerDurationVal}>Muy corto</span> <VeryShortHand />{" "}
          </>
        );
      case minutes >= 5 && minutes <= 10:
        return (
          <>
            <span className={styles.makerDurationVal}>Corto</span>
            <ShortHand />{" "}
          </>
        );
      case minutes >= 10:
        return (
          <>
            <span className={styles.makerDurationVal}>No tan corto</span> <LongHand />{" "}
          </>
        );
      default:
        return;
    }
  };
  return (
    <div className={styles.maker}>
      <Avatar image={author?.Profile?.avatar} />
      <div className={styles.makerUser}>{author.Profile ? author.displayName : "Sin Autor"}</div>
      {contentType !== "Comunidad" && (
        <>
          <span className={styles.makerBar}>|</span>
          <div className={styles.makerDuration}>
            <span className={styles.makerType}>Duración</span>
            {episodeSelected && isEpisode ? `${minutes}:00min` : durationIconHandler(minutes)}
          </div>
        </>
      )}
    </div>
  );
};

export default DurationAuthorInfo;
