import React from "react";
import styles from "./CarouselHero.module.scss";
import CarouselWrapper from "./CarouselWrapper/CarouselWrapper";
import CarouselItem from "./CarouselItem/CarouselItem";
import Loading from "../UI/Loading/Loading";
const Hero = ({ name = "Action", content }) => {
  return (
    <div className={`${styles.hero} ${content ? styles.opacityAnimation : styles.isLoading}`}>
      {content ? (
        <>
          <CarouselWrapper length={content.length}>
            {content.map((video, index) => {
              return <CarouselItem video={video} index={index} fromHome={true} />;
            })}
          </CarouselWrapper>{" "}
        </>
      ) : (
        <Loading />
      )}
    </div>
  );
};

export default Hero;
