import React from "react";
import styles from "./GoBack.module.scss";
import { ReactComponent as GoBackSVG } from "../../../assets/arrow-left.svg";
import { useHistory } from "react-router-dom";
import { connect } from "react-redux";

function GoBack({ isSearching, fromModal, fromModalHandler }) {
  const history = useHistory();

  const onClickHandler = () => {
    if (fromModal) {
      fromModalHandler();
    } else {
      if (isSearching) history.goBack();
      else history.push("/");
    }
  };
  return (
    <div className={`${styles.goBack} ${fromModal && styles.fromModal}`} onClick={onClickHandler}>
      <div className={styles.goBackArrowContainer}>
        <GoBackSVG />
      </div>
      <span>Volver</span>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    isSearching: state.searchReducer.isSearching,
  };
};

export default connect(mapStateToProps)(GoBack);
