import unauthContentService from "../services/contentUnauth";
import * as types from "../actions/ActionTypes";
import _ from "lodash";
import { store } from "../storage/store";

const tenMinutes = 600000;
export const query = (params) => {
  const secret = store.getState().authReducer.secret;
  const service = new unauthContentService(secret);

  return {
    type: types.CONTENTS,
    payload: service["query"](params),
  };
};

export const get = (params) => {
  const secret = store.getState().authReducer.secret;
  const service = new unauthContentService(secret);

  return {
    type: types.CONTENT,
    payload: service["get"](params),
  };
};

export const checkMedia = (hash) => {
  const secret = store.getState().authReducer.secret;
  const service = new unauthContentService(secret);

  return async (dispatch) => {
    return dispatch({
      type: types.CONTENT_CHECK_MEDIA,
      payload: service["checkMedia"](hash),
    }).then((res) => {
      return res;
    });
  };
};

