export const setTopByCategory = (cont, type) => {
  return {
    type: types.SET_TOP_CONTENT_BY_CATEGORY,
    payload: {
      cont: cont,
      type: type,
    },
  };
};

export const setContentByCategory = (cont, type) => {
  return {
    type: types.SET_CONTENT_BY_CATEGORY,
    payload: {
      cont: cont,
      type: type,
    },
  };
};

export const getSerieById = (uuid) => {
  const secret = store.getState().authReducer.secret;
  const service = new unauthContentService(secret);
  return {
    type: types.CONTENT_SERIE_BY_ID,
    payload: service["getSerieById"](uuid),
  };
};
