import React from "react";
import styles from "./ModalAlertCarousel.module.scss";
import { CarouselProvider, Slider, ButtonBack, ButtonNext, Dot, Slide, Image } from "pure-react-carousel";
import FirstCarousel from "../../../../assets/flixxo-login1.png";
import SecondCarousel from "../../../../assets/flixxo-login2.png";
import ThirdCarousel from "../../../../assets/flixxo-login3.png";
import FourthCarousel from "../../../../assets/flixxo-login4.png";
import "pure-react-carousel/dist/react-carousel.es.css";
import { useMediaQuery } from "react-responsive";
const CAROUSEL_ITEMS = [
  {
    image: FirstCarousel,
    title: "TODAS LAS EMOCIONES EN 5 MINUTOS",
  },
  {
    image: SecondCarousel,
    title: "DISFRUTÁ TUS SERIES, ESTÉS DONDE ESTÉS",
  },
  {
    image: ThirdCarousel,
    title: `SOS CREADOR DE CONTENIDO \n Y QUERÉS SUBIR VIDEOS`,
  },
  {
    image: FourthCarousel,
    title: "GANÁ FLIXX Y DESCUBRÍ COMO FUNCIONA",
  },
];

const ModalAlertCarousel = () => {
  const isMobile = useMediaQuery({ query: "(max-width: 600px)" });
  return (
    <div className={styles.modalAlertCarousel}>
      <CarouselProvider
        isIntrinsicHeight={true}
        isPlaying={true}
        interval={5000}
        naturalSlideWidth={547}
        naturalSlideHeight={491}
        totalSlides={CAROUSEL_ITEMS.length}
        dragEnabled={false}
        dragStep={true}
        infinite={true}
        lockOnWindowScroll={true}
      >
        <Slider className={styles.modalAlertCarousel_slider} moveThreshold={0.1}>
          {CAROUSEL_ITEMS.map((item, index) => {
            return (
              <Slide index={index}>
                <div className={styles.modalAlertCarousel_slide}>
                  <div className={styles.modalAlertCarousel_slideImgContainer}>
                    <Image src={item.image} alt={item.title} />
                  </div>

                  <h2>
                    <span>{index === 2 ? "¿" : "¡"}</span>
                    {item.title}
                    <span>{index === 2 ? "?" : "!"}</span>
                  </h2>
                </div>
              </Slide>
            );
          })}
        </Slider>
        <div className={styles.modalAlertCarousel_dotContainer}>
          {CAROUSEL_ITEMS.map((sli, index) => {
            return <Dot className={styles.dot} slide={index}></Dot>;
          })}
        </div>
      </CarouselProvider>
    </div>
  );
};

export default ModalAlertCarousel;
