import React, { useEffect, useState, useRef } from "react";
import Nav from "../../components/Nav/Nav";
import styles from "./Home.module.scss";
import { connect } from "react-redux";
import { logout, initClient } from "../../store/actions/auth";
import { query as getCategories } from "../../store/actions/categorieUnauth";
import { getByUser } from "../../store/actions/content";
import PlayerLanding from "../../components/PlayerLanding/PlayerLanding";
import {
  getByCategory,
  query as getTopContent,
  setTopByCategory,
  setContentByCategory,
  getSerieById,
  get as getContentById,
} from "../../store/actions/contentUnauth";
import * as constants from "../../utils/constants";
import {
  me as onGetUserMe,
  balance as userBalance,
  followedCategoriesGet,
  contentPurchased,
  toggleAllowExitUpload,
  reset as userReset,
  changeCommunityFilter,
} from "../../store/actions/user";
import { getFolloweds } from "../../store/actions/follow";
import { reward as getCurrentReward } from "../../store/actions/ad";
import Hero from "../../components/Carousel/CarouselHero";
import Slider from "../../components/Slider/Slider";
import {
  COMMUNITY_FILTER,
  CONTENT_NUMBER_FILTER,
  SERIES_UUID,
  COMMUNITY_UUID,
  RECOMENDS_UUID,
} from "../../utils/constants";
import {
  getCarrusels,
  getCarruselsContent,
  getCarruselsSeries,
  setCarruselsWithContent,
} from "../../store/actions/carrusels";
import SliderSkeleton from "../../components/UI/SliderSkeleton/SliderSkeleton";
import FacebookLogin from "react-facebook-login";
import useInfiniteScroll from "../../utils/hooks/useInfiniteScroll";
import environment from "environment";
import { findFirstNotPurchased } from "../../utils/common";
const Home = ({
  isLoggedIn,
  communityFilter,
  carruselWithContent,
  setTopByCategory,
  contentByCategory,
  topContentByCategory,
  setContentByCategory,
  setCarruselsWithContent,
  contentPurchasedArray,
  getCategories,
  allCategories,
  getByCategory,
  getCarrusels,
  getCarruselsContent,
  getCarruselsSeries,
  getByUser,
  getSerieById,
  getContentById,
  weblinkURL,
}) => {
  const isInitialMount = useRef(true);

  useEffect(() => {
    const isLoggedInContent = async () => {
      const response = await getByUser(communityFilter);
      if (communityFilter === "series" && contentPurchasedArray) {
        const data = response.value.contents.slice(0, 5).map(async (item) => {
          const response = await getSerieById(item.uuid);
          const [firstNotPurchased, episodeNumber] = findFirstNotPurchased(
            contentPurchasedArray,
            response.value.season[0].content
          );
          return {
            ...response.value,
            ...firstNotPurchased,
            serieUuid: item.uuid,
            media: response.value.media,
            episodeNumber: episodeNumber,
            category: { ...response.value.category },
            author: { ...response.value.author },
          };
        });
        Promise.all(data).then((res) => {
          setTopByCategory(res, communityFilter);
        });
      } else {
        const data = response.value.contents.slice(0, 5).map(async (item) => {
          const response = await getContentById(item.uuid);
          return response.value;
        });
        Promise.all(data).then((res) => {
          setTopByCategory(res, communityFilter);
        });
      }
    };
    if (isLoggedIn) {
      isLoggedInContent();
    }
  }, [isLoggedIn, communityFilter, contentPurchasedArray]);

  useEffect(() => {
    if (communityFilter !== "series") getCategoriesAndContent();
    getCarruselsAndContent();
  }, []);

  useEffect(() => {
    window.scrollTo({
      top: 0,
    });
    if (isInitialMount.current) {
      isInitialMount.current = false;
    } else {
      if (!contentByCategory.community.length || !contentByCategory.series.length) {
        getCategoriesAndContent();
      }
      if (!carruselWithContent.community || !carruselWithContent.series) {
        getCarruselsAndContent();
      }
    }
  }, [communityFilter]);

  const getCategoriesAndContent = () => {
    if (allCategories.length !== 0) getCategoriesSuccess(allCategories);
    else getCategories().then((response) => getCategoriesSuccess(response.value));
  };

  const getCarruselsAndContent = () => {
    getCarrusels().then((response) => {
      getCarruselSuccess(response.value);
    });
  };

  const getCarruselSuccess = (carrusels) => {
    let carruselsWithContentArray = [];
    let topCarrusel = null;
    let getById =
      communityFilter === COMMUNITY_FILTER.COMMUNITY ? getCarruselsContent : getCarruselsSeries;
    let topCarruselCondition =
      communityFilter === COMMUNITY_FILTER.COMMUNITY ? COMMUNITY_UUID : SERIES_UUID;
    if (environment.ENV === "production") topCarruselCondition = RECOMENDS_UUID;
    let promises = carrusels.map((carrusel) => {
      return getById(carrusel.uuid, carrusel.uuid === topCarruselCondition).then((res) => {
        if (carrusel.uuid === topCarruselCondition) {
          topCarrusel = [...res.value.contents].slice(0, 5);
        } else {
          let toSetContent = {
            id: carrusel.uuid,
            name: carrusel.name,
            title: carrusel.title,
            content: res.value.contents,
          };
          carruselsWithContentArray.push(toSetContent);
        }
      });
    });

    Promise.all(promises).then(() => {
      setCarruselsWithContent(carruselsWithContentArray, communityFilter);
      if (!isLoggedIn) setTopByCategory(topCarrusel, communityFilter);
    });
  };

  const getCategoriesSuccess = (categories) => {
    let categoriesWithContentArray = [];
    let categoriesWithTopContentArray = null;

    let withContentLength = contentByCategory[communityFilter].length;

    categories = categories.slice(withContentLength, withContentLength + 5);
    let promises = categories.map((category) => {
      return getByCategory({
        id: category.id,
        contentType: communityFilter,
      }).then((response) => {
        let toSetContent = {
          id: category.id,
          name: category.name,
          content: response.value.contents,
        };
        categoriesWithContentArray.push(toSetContent);
      });
    });

    Promise.all(promises).then(() => {
      setContentByCategory(categoriesWithContentArray, communityFilter);
    });
  };

  let hasDataLeft = allCategories.length !== contentByCategory[communityFilter].length;
  const sliderCheck =
    communityFilter === "series"
      ? !carruselWithContent[communityFilter]
      : !contentByCategory[communityFilter].length;

  const [setElement] = useInfiniteScroll(getCategoriesSuccess, allCategories, hasDataLeft);
  return (
    <main className={styles.home}>
      {weblinkURL && <PlayerLanding fromHome />}
      <Hero content={topContentByCategory[communityFilter]} />
      {!topContentByCategory[communityFilter] || sliderCheck ? <SliderSkeleton /> : null}
      <section className={styles.home_sliders}>
        {topContentByCategory[communityFilter] &&
          carruselWithContent[communityFilter] &&
          carruselWithContent[communityFilter].map((carrusel) => {
            return carrusel.content.length > 0 ? (
              <Slider name={carrusel.title} key={carrusel.id} content={carrusel.content} />
            ) : null;
          })}
        {communityFilter !== "series" &&
          topContentByCategory[communityFilter] &&
          contentByCategory[communityFilter] &&
          contentByCategory[communityFilter].map((categorie) => {
            return categorie.content.length > 0 ? (
              <Slider name={categorie.name} key={categorie.id} content={categorie.content} />
            ) : null;
          })}
        {(!topContentByCategory[communityFilter] && !contentByCategory[communityFilter].length) ||
        communityFilter === "series" ? null : (
          <div ref={setElement} style={{ background: "transparent", width: "100%" }}></div>
        )}
        {contentByCategory[communityFilter].length &&
          communityFilter !== "series" &&
          hasDataLeft && <SliderSkeleton />}
      </section>
    </main>
  );
};

const mapStateToProps = (state) => {
  return {
    isLoggedIn: state.authReducer.isLoggedIn,
    secret: state.authReducer.secret,
    user: state.userReducer.user,
    balance: state.userReducer.balance,
    showSearch: state.searchReducer.showSearch,
    communityFilter: state.userReducer.communityFilter,
    playingVideo: state.contentReducer.playingVideo,
    allCategories: state.categorieReducer.categories,
    contentPurchasedArray: state.userReducer.contentPurchased,
    carruselWithContent: state.carruselsReducer.carruselWithContent,
    topContentByCategory: state.contentReducer.topContentByCategory,
    contentByCategory: state.contentReducer.contentByCategory,
    weblinkURL: state.contentReducer.weblinkURL,
  };
};

export default connect(mapStateToProps, {
  getCarruselsContent,
  setCarruselsWithContent,
  setTopByCategory,
  setContentByCategory,
  getCarruselsSeries,
  getCarrusels,
  onGetUserMe,
  logout,
  initClient,
  userBalance,
  followedCategoriesGet,
  contentPurchased,
  getFolloweds,
  getCategories,
  getTopContent,
  getByCategory,
  getCurrentReward,
  getByUser,
  getSerieById,
  getContentById,
})(Home);
