import React, { useState, useRef, useEffect } from "react";
import styles from "./Search.module.scss";
import { search, searchText, setSearching, dismissSearch } from "../../../store/actions/search";
import { connect } from "react-redux";
import { useHistory, useLocation } from "react-router-dom";
import LottieWeb from "lottie-web";
import searchData from "../../../assets/search2.json";
import PropTypes from "prop-types";

const Search = ({ search, searchText, setSearching, dismissSearch, isMobile }) => {
  const history = useHistory();
  const location = useLocation();
  const [isClicked, setIsClicked] = useState(false);
  const [searchValue, setSearchValue] = useState("");
  const [searchAnimation, setSearchAnimation] = useState(null);
  const refInput = useRef("");
  const refLottie = useRef(null);

  useEffect(() => {
    if (refLottie && !searchAnimation) {
      let searchAnimation = LottieWeb.loadAnimation({
        container: refLottie.current,
        renderer: "svg",
        loop: false,
        autoplay: false,
        animationData: searchData,
        rendererSettings: {
          preserveAspectRatio: "xMidYMid slice",
          viewBoxOnly: true,
          className: `${styles.searchAnimation} ${isMobile ? styles.isMobile : ""}`,
        },
      });
      setSearchAnimation(searchAnimation);
    }
  }, [refLottie, searchAnimation]);

  const isClickedHandler = () => {
    if (!isClicked) {
      if (!history.location.pathname.includes("search")) {
        history.push("/search");
      }
      setSearching();
      setIsClicked(!isClicked);
      search({ query: "" });
      searchAnimation.playSegments([0, 30], true);
      setTimeout(() => {
        refInput.current.focus();
      }, 500);
    } else {
      dismissSearch();
      closeIsClickedHandler();
    }
  };
  const closeIsClickedHandler = () => {
    searchAnimation.playSegments([30, 0], true);
    setIsClicked(!isClicked);
    setSearchValue("");
    if (history.location.pathname.includes("search")) {
      history.push("/");
    }
  };

  const searchHandler = (value) => {
    setSearchValue(value);
    setTimeout(() => {
      search({ query: searchValue });
      searchText(value);
    }, 1000);
  };

  const animationHandler = () => {
    searchAnimation.goToAndStop(27, true);
    // searchAnimation.pause();
  };

  useEffect(() => {
    if (location.pathname !== "/search" && isClicked) {
      dismissSearch();
      closeIsClickedHandler();
    }
  }, [location]);
  return (
    <div className={styles.search}>
      <div ref={refLottie} onClick={isClickedHandler}></div>
      <div className={`${styles.searchContainer} ${isClicked ? styles.active : ""}`}>
        <input
          name='search'
          id='search'
          value={searchValue}
          placeholder='¿Que estás buscando?'
          onChange={(e) => searchHandler(e.target.value)}
          ref={refInput}
          autoComplete='off'
        />
      </div>
    </div>
  );
};

Search.propTypes = {
  search: PropTypes.func,
  searchText: PropTypes.func,
  setSearching: PropTypes.func,
  dismissSearch: PropTypes.func,
};

export default connect(null, {
  search,
  searchText,
  setSearching,
  dismissSearch,
})(Search);
