import CarruselsService from "..//services/carrusels";
import * as types from "../actions/ActionTypes";
import { store } from "../storage/store";

export const getCarrusels = () => {
  const secret = store.getState().authReducer.secret;
  const service = new CarruselsService(secret);
  return {
    type: types.GET_CARRUSELS_LIST,
    payload: service["get"],
  };
};

export const getCarruselsContent = (uuid, hasBody) => {
  const secret = store.getState().authReducer.secret;
  const service = new CarruselsService(secret);

  return {
    type: types.GET_CARRUSELS_CONTENT,
    payload: service["getCarruselsContent"](uuid, hasBody),
  };
};

export const getCarruselsSeries = (uuid, hasBody) => {
  const secret = store.getState().authReducer.secret;
  const service = new CarruselsService(secret);

  return {
    type: types.GET_CARRUSELS_SERIES,
    payload: service["getCarruselsSeries"](uuid, hasBody),
  };
};

export const setCarruselsWithContent = (cont, type) => {
  return {
    type: types.SET_CARRUSELS_WITH_CONTENT,
    payload: {
      type: type,
      cont: cont,
    },
  };
};
