import _ from "lodash";
import { TYPE_SERIE } from "./constants";

export function getThumbnails(content) {
  return content.media.filter((m) => m.type === 2);
}

export function getTileCoverUrl(content) {
  let tileCover = _.sortBy(
    content.media.filter((m) => m.type === 2),
    "order"
  )[0];
  if (content.contentType === 2)
    tileCover = _.sortBy(
      content.media.filter((m) => m.type === 2),
      "order"
    )[0];
  return tileCover ? getCoverUrl(tileCover) : "";
}

export function getSwitchingThumbUrl(content, i) {
  var thumbUrl = "";
  if (i === 0) {
    var image = content.media.filter((m) => m.type === 1)[0];
    if (image) thumbUrl = getCoverUrl(image);
  } else {
    var thumbs = content.media.filter((m) => m.type === 2 && m.order !== 1);
    thumbs.forEach((thumb, index) => {
      if (index + 1 === i) {
        thumbUrl = getCoverUrl(thumb);
      }
    });
  }

  return thumbUrl;
}

export function getDetailImageUrl(content) {
  if (content) {
    let detailImage = content.media.filter((m) => m.name.includes("thumb"))[0];
    if (!detailImage) detailImage = content.media.filter((m) => m.name.includes("background"))[0];
    return detailImage ? getHeroUrl(detailImage) : "";
  } else return "";
}

export function getMediaForPreview(content) {
  return content
    ? [
        {
          name: "thumb 1",
          order: 1,
          type: 2,
          url: content.thumbnails[0].preview,
        },
        {
          name: "cover",
          order: 0,
          type: 1,
          url: content.thumbnails[0].preview,
        },
        {
          name: "thumb 2",
          order: 2,
          type: 2,
          url: content.thumbnails[1].preview,
        },
        {
          name: "thumb 3",
          order: 3,
          type: 2,
          url: content.thumbnails[2].preview,
        },
      ]
    : [];
}

// FIXME: corregir este metodo!!!
export function getSwitchingThumbUrlSerie(content, type, index, fixme) {
  let image;
  let thumbUrl = "";

  if (fixme === true) {
    if (type === "cover") {
      if (content.cover) {
        return content.cover.sizes.xcover;
      } else {
        image = content.media.filter((m) => m.type === 1)[0];
        if (image) {
          return image.sizes ? image.sizes.xcover : image.url;
        }
      }
    }

    let media = [];
    if (content.contentmedia !== undefined) {
      media = content.contentmedia;
    } else {
      content.media.forEach((item) => {
        if (item.type === 1) {
          media.push(item);
        } else if (item.order !== 1 && item.type === 2) {
          media.push(item);
        }
      });
    }
    media.forEach((thumb, _index) => {
      if (_index === index) {
        thumbUrl = thumb.sizes ? thumb.sizes.xcover : thumb.url;
      }
    });
    return thumbUrl;
  }

  if (type === "cover") {
    if (content.cover) {
      return content.cover.sizes.xcover;
    } else {
      image = content.media.filter((m) => m.type === 1)[0];
      if (image) {
        return image.url;
      }
    }
  }

  let media = [];
  if (content.contentmedia !== undefined) {
    media = content.contentmedia;
  } else {
    content.media.forEach((item) => {
      if (item.type === 2) {
        media.push(item);
      }
    });
  }
  media.forEach((thumb, _index) => {
    if (_index === index) {
      thumbUrl = thumb.url;
    }
  });
  return thumbUrl;
}

