export function getMediaForSeriePreview(mainCover, episodes, index) {
  let episodeMedia = episodes[index].thumbnails.sort((a, b) => (a.order > b.order ? 1 : -1));

  return mainCover && episodes
    ? [
        {
          name: "thumb 1",
          order: 1,
          type: 2,
          url: episodeMedia[0].preview,
        },
        {
          name: "cover",
          order: 0,
          type: 1,
          url: mainCover[0].preview,
        },
        {
          name: "thumb 2",
          order: 2,
          type: 2,
          url: episodeMedia[1].preview,
        },
        {
          name: "thumb 3",
          order: 3,
          type: 2,
          url: episodeMedia[2].preview,
        },
      ]
    : [];
}

export function getMediaForSerie(mainCover, episodes, index) {
  let episodeMedia = episodes[index].media.sort((a, b) => (a.order > b.order ? 1 : -1));

  return mainCover && episodes
    ? [
        {
          name: "thumb 1",
          order: 1,
          type: 2,
          url: episodeMedia[0].url,
          sizes: episodeMedia[0].sizes,
        },
        {
          name: "cover",
          order: 0,
          type: 1,
          url: mainCover[0].preview === undefined ? mainCover[0].url : mainCover[0].preview,
          sizes: mainCover[0].preview === undefined ? mainCover[0].sizes : null,
        },
        {
          name: "thumb 2",
          order: 2,
          type: 2,
          url: episodeMedia[1].url,
          sizes: episodeMedia[1].sizes,
        },
        {
          name: "thumb 3",
          order: 3,
          type: 2,
          url: episodeMedia[2].url,
          sizes: episodeMedia[2].sizes,
        },
      ]
    : [];
}

export function getMainCoverId(content) {
  if (content) {
    const mainCover = content.media.filter((m) => m.type === 2 && m.order === 1)[0];
    return mainCover ? mainCover.id : "";
  } else return "";
}

export function getThumbsIds(content) {
  var thumbsIds = [];
  if (content) {
    thumbsIds.push(content.media.filter((m) => m.type === 1)[0].id);
    content.media
      .filter((m) => m.type === 2 && m.order !== 1)
      .forEach((m) => {
        thumbsIds.push(m.id);
      });
  }
  return thumbsIds;
}

export function getMainCoverToUpload(content, hash) {
  return {
    hash: hash,
    image: content.mainCover[0],
    name: content.mainCover[0].name,
    type: 2,
    order: 1,
  };
}

export function getThumbToUpload(thumb, index, hash) {
  return {
    hash: hash,
    image: thumb,
    name: thumb.name,
    type: index === 0 ? 1 : 2,
    order: index === 0 ? 0 : index + 1,
  };
}

export function getRevMainCoverToUpload(content, uuid) {
  return {
    uuid: uuid,
    image: content.mainCover[0],
    name: content.mainCover[0].name,
    type: 2,
    order: 1,
  };
}

export function getRevThumbToUpload(thumb, index, uuid) {
  return {
    uuid: uuid,
    image: thumb,
    name: thumb.name,
    type: index === 0 ? 1 : 2,
    order: index === 0 ? 0 : index + 1,
  };
}

export function isMainCover(image) {
  return image.type === 2 && image.order === 1;
}

export function getHeroUrl(image) {
  return image.sizes ? image.sizes.mainscreen : image.url;
}

function getCoverUrl(image) {
  return image.sizes ? image.sizes.xcover : image.url;
}

export function getHero(content, type) {
  if (content) {
    const media = content.media.find((m) => m.order === 2 || m.order === 1);
    if (!media) {
      return content.media[0]?.url;
    } else {
      const mediaImg = type ? media.sizes[type] : media.sizes.hero;
      return mediaImg || "";
    }
  } else {
    return "";
  }
}

function getMainCoverURL(image, type) {
  if (type === "xcover") {
    return image.sizes ? image.sizes.xcover : image.url;
  } else {
    return image.sizes ? image.sizes.hero : image.url;
  }
}

export function getMainCover(content, type) {
  let tileCover = _.sortBy(
    content.media.filter((m) => m.type === 2),
    "order"
  )[0];
  if (content.contentType === 2)
    tileCover = _.sortBy(
      content.media.filter((m) => m.type === 2),
      "order"
    )[0];
  return tileCover ? getMainCoverURL(tileCover, type) : "";
}

export function getMainThumb(content, type) {
  let tileCover = content.media.find((m) => m.order === 3);
  // if (content.contentType === 2)
  //   tileCover = _.sortBy(
  //     content.media.filter((m) => m.type === 2),
  //     "order"
  //   )[0];
  return tileCover ? getMainCoverURL(tileCover, type) : "";
}
