import React, { useEffect, useCallback } from "react";
import styles from "./PlayerLanding.module.scss";
import { connect } from "react-redux";
import VideoPlayer from "../VideoPlayer/VideoPlayer";
import NextEpisode from "../UI/NextEpisode/NextEpisode";
import { ReactComponent as Close } from "../../assets/close2.svg";
import {
  setPlayerDuration,
  setPlayerEnd,
  setPlayerProgress,
  setPlayerStart,
  setBufferStart,
  setBufferEnd,
  resetNextEpisodeValues,
} from "../../store/actions/media";
import {
  removeContentWeblinkURL,
  removeVideoSubtitles,
  setCurrentContentForLanding,
} from "../../store/actions/content";
import { useHistory } from "react-router-dom";
import CastButton from "../UI/CastButton/CastButton";
const PlayerLanding = ({
  isEpisode,
  weblinkURL,
  bufferEnd,
  setPlayerEnd,
  currentSeason,
  setBufferStart,
  setBufferEnd,
  resetNextEpisodeValues,
  currentSerieLanding,
  setPlayerStart,
  currentContentLanding,
  setPlayerProgress,
  setPlayerDuration,
  removeContentWeblinkURL,
  subtitlesArray,
  fromHome,
  videoSubtitles,
  removeVideoSubtitles,
  setCurrentContentForLanding,
}) => {
  const history = useHistory();

  const exitHandler = () => {
    let currentEpi = currentSerieLanding.season[0].content.findIndex((cont, index) => {
      return currentContentLanding.uuid === cont.uuid;
    });
    let nextEpisode = currentSerieLanding.season[0].content[currentEpi + 1];
    if (nextEpisode) {
      setCurrentContentForLanding({
        ...nextEpisode,
        category: { ...currentSerieLanding.category },
        author: { ...currentSerieLanding.author },
        episodeNumber: currentEpi + 2,
      });
    } else {
      history.push("/");
    }
  };
  const endHandler = () => {
    setPlayerEnd();
    removeContentWeblinkURL();
    resetNextEpisodeValues();
    removeVideoSubtitles();
    if (isEpisode && !fromHome) exitHandler();
  };

  const escFunction = useCallback((event) => {
    if (event.keyCode === 27) {
      endHandler();
    }
  }, []);

  useEffect(() => {
    document.addEventListener("keydown", escFunction, false);
    return () => {
      document.removeEventListener("keydown", escFunction, false);
    };
  }, []);

  return (
    <div className={styles.videoContainer}>
      <Close className={styles.videoContainerClose} onClick={endHandler} />
      {weblinkURL ? (
        <VideoPlayer
          onBuffer={() => {
            setBufferStart();
          }}
          onReady={() => {
            setBufferEnd();
          }}
          url={weblinkURL}
          playing={true}
          onDuration={(dur) => setPlayerDuration(dur)}
          onProgress={(progr) => setPlayerProgress(progr)}
          onEnded={endHandler}
          width={"100%"}
          height={"100%"}
          subtitles={videoSubtitles}
        />
      ) : null}
      {isEpisode && !fromHome ? (
        <NextEpisode
          currentSeason={currentSeason}
          currentSerieLanding={currentSerieLanding}
          currentContentLanding={currentContentLanding}
        />
      ) : null}
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    currentContentLanding: state.contentReducer.currentContentLanding,
    currentSerieLanding: state.contentReducer.currentSerieLanding,
    weblinkURL: state.contentReducer.weblinkURL,
    videoSubtitles: state.contentReducer.videoSubtitles,
    hasEnded: state.mediaReducer.playerHasEnd,
    bufferEnd: state.mediaReducer.bufferEnd,
  };
};

export default connect(mapStateToProps, {
  setPlayerProgress,
  setPlayerEnd,
  setBufferStart,
  setBufferEnd,
  resetNextEpisodeValues,
  setPlayerDuration,
  removeContentWeblinkURL,
  setPlayerStart,
  removeVideoSubtitles,
  setCurrentContentForLanding,
})(PlayerLanding);
