import React from "react";
import { ReactComponent as FlixxoCoin } from "../../../assets/flixxo-coin.svg";
import CountUp from "react-countup";
import Lottie from "react-lottie";
import Coins from "../../../assets/flixxo-coins.json";
import { motion } from "framer-motion";
import styles from "./UserBalance.module.scss";
import PropTypes from "prop-types";

const coinOptions = {
  loop: false,
  autoplay: false,
};

const lottieStyle = {
  position: "absolute",
  top: "-10px",
  right: "-10px",
};

const UserBalance = ({ controls, prevAmount, amount, isStopped, eventListenerCoin }) => {
  return (
    <div className={`${styles.userBalance__coins}`}>
      <FlixxoCoin className={styles.userBalance__svg} />
      <motion.span animate={controls} style={{ color: "#FFFFFF" }}>
        <CountUp start={prevAmount} end={amount} duration={2} decimals={2} decimal='.'></CountUp>
      </motion.span>
      <Lottie options={{ animationData: Coins, ...coinOptions }} width={300} speed={1.5} style={lottieStyle} isClickToPauseDisabled={true} isStopped={isStopped} eventListeners={eventListenerCoin} />
    </div>
  );
};

UserBalance.propTypes = {
  prevAmount: PropTypes.string,
  amount: PropTypes.string,
  controls: PropTypes.func,
  isStopped: PropTypes.bool,
  eventListenerCoin: PropTypes.arrayOf(
    PropTypes.shape({
      eventName: PropTypes.string,
      callback: PropTypes.func,
    })
  ),
};

export default UserBalance;
