import * as types from "../actions/ActionTypes";
import _ from "lodash";

const defaultState = {
  torrents: [],
  client: {},
  port: null,
  network: false,
  networkTestError: {},
};

export default function reducer(state = defaultState, action) {
  switch (action.type) {
    case types.TORRENT_PUSH:
      return {
        ...state,
        torrents: [...state.torrents, action.payload],
      };

    case types.TORRENT_REMOVE:
      return {
        ...state,
        torrents: state.torrents.filter((torrent) => torrent.infoHash !== action.payload),
      };

    case types.TORRENT_CLIENT:
      return {
        ...state,
        client: action.payload,
      };

    case types.TORRENT_PORT:
      return {
        ...state,
        port: action.payload,
      };

    case types.TORRENT_NETWORK_TEST_REJECTED:
      return Object.assign({}, state, {
        networkTestError: action.payload.response,
      });

    case types.TORRENT_UPDATE:
      let torrents = _.cloneDeep(state.torrents);
      const index = torrents.findIndex((t) => {
        return t.infoHash === action.payload.hash;
      });

      if (index !== -1) {
        torrents[index] = Object.assign({}, torrents[index], action.payload.options);
      }

      return Object.assign({}, state, {
        torrents: torrents,
      });

    default:
      return state;
  }
}
