import AuthService from "../services/auth";
import AppService from "../services/app";
import * as types from "../actions/ActionTypes";

const authService = new AuthService();
const appService = new AppService();

const loginVersionErrorName = "DeprecatedClientError";

const getLink = (downloadLinks) => {
  let OSName = "Unknown OS";
  if (navigator.appVersion.indexOf("Win") !== -1) OSName = "windows";
  if (navigator.appVersion.indexOf("Mac") !== -1) OSName = "mac";
  if (navigator.appVersion.indexOf("X11") !== -1) OSName = "unix";
  if (navigator.appVersion.indexOf("Linux") !== -1) OSName = "linux";

  return downloadLinks[OSName];
};

const createVersionError = (versionError, downloadLinks) => {
  versionError.error["link"] = getLink(downloadLinks);
  return {
    type: types.LOGIN_VERSION_ERROR,
    payload: versionError,
  };
};

const getLinksForVersionError = (error) => {
  return (dispatch) => {
    return dispatch({
      type: types.HANDLE_LOGIN_VERSION_ERROR,
      payload: appService["getInfo"](),
    }).then((response) => {
      dispatch(createVersionError(error, response.action.payload.downloadLink));
    });
  };
};

