export const login = (email, password) => {
  return {
    type: types.LOGIN,
    payload: authService["login"]({
      email: email,
      password: password,
    }).then((res) => {
      if (!res.success) {
        throw res;
      }
      return res;
    }),
    meta: email,
  };
};

export const loginExternal = (provider, accessToken) => {
  return (dispatch) => {
    return dispatch({
      type: types.LOGIN,
      payload: authService["loginExternal"](provider, { code: accessToken }).then((res) => {
        if (!res.success) throw res;
        else return res;
      }),
      meta: {
        accessToken: accessToken,
      },
    }).catch((exc) => {
      if (exc.response.error.name === loginVersionErrorName) {
        dispatch(getLinksForVersionError(exc.response));
      }
    });
  };
};

export const register = (params) => ({
  type: types.REGISTER,
  payload: authService["register"](params).then((res) => {
    if (!res.success) {
      throw res;
    }
    return res;
  }),
  meta: {
    email: params.email,
  },
});

export const checkRegister = (params) => ({
  type: types.CHECK,
  payload: authService["check"](params),
  meta: {
    email: params.email,
  },
});

export const clearRegisterError = () => ({
  type: types.REGISTER_CLEAR_ERROR,
});

export const confirm = (params) => ({
  type: types.CONFIRM,
  payload: authService["confirm"](params).then((res) => {
    if (!res.success) {
      throw res;
    }
    return res;
  }),
});

export const changepassword = (currentPassword, newPassword) => ({
  type: types.CHANGE_PASSWORD,
  payload: authService["changepassword"]({
    currentPassword,
    newPassword,
  }),
});

