export const forgotPassword = (mobileNumber, method) => ({
  type: types.FORGOT_CODE,
  payload: authService["forgotCode"](mobileNumber, method).then((res) => {
    if (!res.success) {
      throw res;
    }
  }),
  meta: {
    email: mobileNumber,
  },
});

export const resendCode = (email) => ({
  type: types.RESEND_CODE,
  payload: authService["resendCode"](email),
});

export const resetPassword = (params) => ({
  type: types.RESET_PASSWORD,
  payload: authService["resetPassword"](params),
});

export const logout = () => {
  return {
    type: "LOGOUT",
  };
};

export const initClient = () => {
  return {
    type: "INIT_CLIENT",
  };
};

export const showAlertModal = (type) => {
  return {
    type: types.ALERT_MODAL_OPEN,
    payload: type,
  };
};

export const setPreviousAlertModal = (type) => {
  return {
    type: types.ALERT_MODAL_PREVIOUS,
    payload: type,
  };
};

export const closeAlertModal = () => {
  return {
    type: types.ALERT_MODAL_CLOSE,
  };
};

export const openSuccessModal = () => {
  return {
    type: types.SUCCESS_MODAL_OPEN,
  };
};
export const closeSuccessModal = () => {
  return {
    type: types.SUCCESS_MODAL_CLOSE,
  };
};

export const authCheck = (body) => {
  return {
    type: types.AUTH_CHECK,
    payload: authService["authCheck"](body),
  };
};
