import React, { useState, useRef, useEffect } from "react";
import styles from "./ButtonPurchase.module.scss";
import LottieWeb from "lottie-web";
import Lottie from "react-lottie";
import loadingData from "../../../assets/flixxo-loading-button.json";
import ButtonData from "../../../assets/flixxo-watch-now-button.json";
import ButtonInitData from "../../../assets/flixxo-purchase-init.json";
import ButtonEndData from "../../../assets/flixxo-purchase-end.json";

// const defaultOptions = {
//   loop: false,
//   autoplay: true,
//   rendererSettings: {
//     preserveAspectRatio: "xMidYMid slice",
//     className: styles.success,
//   },
// };

const eventListeners = [
  {
    eventName: "complete",
  },
];

const defaultOptions = {
  loop: false,
  autoplay: true,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
    className: styles.suc,
  },
};

const Button = ({ fromLanding, children, onClick, isPrimary, isSecondary, changeStatus, animation, customStyle, isLoading, ...props }) => {
  const lottieLogoRef = useRef(null);
  const [lottieLogoAnimation, setLottieLogoAnimation] = useState(null);
  const [animationPlaying, setAnimationPlaying] = useState(false);

  useEffect(() => {
    if (lottieLogoRef && !lottieLogoAnimation) {
      let lottieAnimation = LottieWeb.loadAnimation({
        container: lottieLogoRef.current,
        renderer: "svg",
        loop: false,
        autoplay: false,
        animationData: ButtonInitData,
        rendererSettings: {
          preserveAspectRatio: "xMidYMid slice",
          viewBoxOnly: true,
          className: styles.svg,
        },
      });
      //   lottieAnimation.setSpeed(1.5);
      setLottieLogoAnimation(lottieAnimation);
    }

    if (lottieLogoAnimation) {
      //COMPRAR
      lottieLogoAnimation.addEventListener("complete", () => {
        setAnimationPlaying(!animationPlaying);
      });
    }

    // if (lottieLogoRef && lottieLogoAnimation) {
    //   if (changeStatus && !animationPlaying) {
    //     lottieLogoAnimation.goToAndStop(149, true);
    //   }
    //   if (!changeStatus && !animationPlaying) {
    //     lottieLogoAnimation.goToAndStop(0, true);
    //   }
    // }
  }, [lottieLogoRef, lottieLogoAnimation, isLoading]);

  const clickHandler = () => {
    // if (!changeStatus) {
    //   setAnimationPlaying(true);
    //   lottieLogoAnimation.playSegments([0, 149], true);
    //   lottieLogoAnimation.addEventListener("complete", () => {
    //     setAnimationPlaying(false);
    //   });
    // }
    lottieLogoAnimation.play();
    // setAnimationPlaying(!animationPlaying);
  };

  {
    /* <button className={`${styles.buttonModalAlert} ${fromLanding ? styles.fromLanding : ""} ${animation || ""}  ${customStyle || ""} ${changeStatus ? styles.changeStatus : ""}`} onClick={onClick}>
        {isLoading ? <Lottie options={{ animationData: loadingData, ...defaultOptions }} width={50} speed={1} /> : children}
      </button> */
  }

  return (
    <>
      <div
        ref={lottieLogoRef}
        onClick={() => {
          //   onClick();
          clickHandler();
        }}
      />
      {animationPlaying ? <Lottie options={{ animationData: ButtonEndData, ...defaultOptions }} eventListeners={eventListeners} speed={0.7} /> : null}
    </>
  );
};

export default Button;
