import MediaService from "../services/media";
import * as types from "../actions/ActionTypes";
import { store } from "../storage/store";

export const post = (params) => {
  const secret = store.getState().authReducer.secret;
  const service = new MediaService(secret);

  return {
    type: types.MEDIA_POST,
    payload: service["post"](params),
  };
};

export const setPlayerProgress = (progress) => {
  return {
    type: types.SET_PLAYER_PROGRESS,
    payload: progress,
  };
};

export const setPlayerDuration = (duration) => {
  return {
    type: types.SET_PLAYER_DURATION,
    payload: duration,
  };
};

export const setPlayerEnd = () => {
  return {
    type: types.SET_PLAYER_END,
  };
};

export const setPlayerStart = () => {
  return {
    type: types.SET_PLAYER_START,
  };
};

export const setPrevPlayerProgress = (progress) => {
  return {
    type: types.SET_PREV_PLAYER_PROGRESS,
    payload: progress,
  };
};

export const setShowNextEpisode = () => {
  return {
    type: types.SHOW_NEXT_EPISODE,
  };
};

export const setDismissNextEpisode = () => {
  return {
    type: types.DISMISS_NEXT_EPISODE,
  };
};

export const resetNextEpisodeValues = () => {
  return {
    type: types.RESET_NEXT_EPISODE_VALUE,
  };
};

export const setBufferEnd = () => {
  return {
    type: types.SET_BUFFER_END,
  };
};

export const setBufferStart = () => {
  return {
    type: types.SET_BUFFER_START,
  };
};
