import React, { useState } from "react";
import styles from "./ContentActions.module.scss";
import { ReactComponent as FlixxoCoin } from "../../../assets/flixxo-coin.svg";
import { flixxos } from "../../../utils/constants";
import PropTypes from "prop-types";
import Text from "../Text/Text";
import Button from "../ButtonForm/ButtonForm";

const ContentActions = ({
  contentType,
  canPurchase,
  isPurchased,
  isLoggedIn,
  price = "0.00",
  fromNextEpisode,
  primaryButtonHandler,
  secondaryButtonHandler,
  episodeNumber = 1,
}) => {
  const [isHovered, setIsHovered] = useState(false);
  const buttonContentHandler = () => {
    if (isLoggedIn) {
      if (isPurchased) {
        return "REPRODUCIR";
      } else if (canPurchase && !isPurchased) {
        return "VER AHORA";
      } else if (!canPurchase && !isPurchased) {
        return isHovered ? "VER PUBLICIDAD" : "VER AHORA";
      }
    } else {
      return "VER AHORA";
    }
  };

  const contentStatusHandler = () => {
    if (!isPurchased) {
      return `${flixxos(price)} Flixx`;
    } else if (isPurchased) {
      return "Disponible!";
    }
  };

  return (
    <div className={`${styles.contentActions} ${contentType && styles[contentType]}`}>
      <div className={styles.contentActions_price}>
        {contentType === "series" && (
          <Text tag='span' weight='medium' size={26} customStyle={styles.contentActions_episode}>
            Episodio {episodeNumber} -
          </Text>
        )}
        {!isPurchased && <FlixxoCoin className={styles.contentActions_coin} />}
        <Text tag='span' weight='medium' size={26} customStyle={styles.contentActions_flixx}>
          {contentStatusHandler()}
        </Text>
      </div>

      <div
        className={`${styles.contentActions_actions} ${contentType === "series" && styles.series}`}
      >
        <div className={styles.contentActions_actions_main}>
          <Button
            hasPlay={canPurchase || !isLoggedIn || isPurchased}
            colorType={isLoggedIn && !canPurchase && !isPurchased && isHovered ? "purple" : ""}
            onClick={primaryButtonHandler}
            onPointerEnter={() => setIsHovered(!isHovered)}
            onPointerLeave={() => setIsHovered(!isHovered)}
            hasHover
          >
            {buttonContentHandler()}
          </Button>
          {isLoggedIn && !canPurchase && !isPurchased && (
            <Text
              tag='span'
              weight='semibold'
              size={13}
              color='error'
              customStyle={`${styles.contentActions_error} ${isHovered && styles.hovered}`}
            >
              ¡No tenés suficientes Flixx!
            </Text>
          )}
        </div>
        {(contentType || fromNextEpisode) && (
          <Button
            colorType='gray'
            hasInfo={!fromNextEpisode}
            onClick={secondaryButtonHandler}
            customStyle={styles.secondary}
          >
            {fromNextEpisode ? "OMITIR" : "MÁS INFO"}
          </Button>
        )}
      </div>
    </div>
  );
};

ContentActions.propTypes = {
  contentType: PropTypes.oneOf(["", "series", "community"]),
  canPurchase: PropTypes.bool,
  isPurchased: PropTypes.bool,
  isLoggedIn: PropTypes.bool,
  price: PropTypes.string,
  fromNextEpisode: PropTypes.bool,
};
export default ContentActions;
