import PaymentService from "../services/payment";
import * as types from "../actions/ActionTypes";
import { store } from "../storage/store";


export const content = params => {
  const secret = store.getState().authReducer.secret;
  const service = new PaymentService(secret);

  return {
    type: types.PAYMENT_CONTENT_POST,
    payload: service["content"](params)
  };
};

export const boost = id => {
  const secret = store.getState().authReducer.secret;
  const service = new PaymentService(secret);

  let body = { contentId: id }
  return {
    type: types.PAYMENT_CONTENT_BOOST,
    payload: service["boost"](body)
  };
}

export const applyPayTicket = ticket => {
  const secret = store.getState().authReducer.secret;
  const service = new PaymentService(secret);

  return {
    type: types.PAYMENT_APPLY_TICKET,
    payload: service["applyPayTicket"](ticket)
  };
}

export const clearBoostError = () => {
  return {
    type: types.CLEAR_PAYMENT_BOOST_ERROR
  }
}
