import ApiClient from "./apiclient";

export default class UserService extends ApiClient {
  async profile(params) {
    return this.PATCH("/me/profile", params);
  }

  async me() {
    return this.GET("/me");
  }

  async wallet() {
    return this.GET("/me/cashin/wallet");
  }

  async walletPost() {
    return this.POST("/me/cashin/wallet");
  }

  async balance() {
    return this.GET("/me/balance");
  }

  async movements(params) {
    let apiUrl = "/wallet/movements";
    let obj = {
      filter: params.filter,
      from: params.from || undefined,
      to: params.to || undefined,
    };
    return this.GET(`${apiUrl}`, obj);
  }

  async myTopContents(from, to) {
    return from !== undefined && to !== undefined ? this.GET(`/me/contents/top?from=${from}&to=${to}`) : this.GET("/me/contents/top");
  }

  async myAllContents(from, to) {
    return from !== undefined && to !== undefined ? this.GET(`/me/contents?from=${from}&to=${to}`) : this.GET("/me/contents");
  }

  async getContent(uuid) {
    return this.GET(`/me/contents/${uuid}`);
  }

  async seededContent() {
    return this.GET("/me/contents/seeded");
  }

  async setMobileNumber(params) {
    return this.PUT("/auth/mobilenumber", params);
  }

  async contentBalance(uuid) {
    const url = `/me/balance/${uuid}`;
    return this.GET(url);
  }

  async countries() {
    return this.GET("/countries");
  }

  async followedCategoriesPost(categories) {
    return this.POST("/me/followed_categories", categories);
  }

  async followedCategoriesGet() {
    return this.GET("/me/followed_categories");
  }

  async seedPost(param) {
    return this.POST("/me/clientkeys", param, {
      headers: {
        "Content-Type": "text/plain",
      },
    });
  }

  async contentPurchased(param) {
    return this.GET("/me/contents/purchased", { contentType: param });
  }

  async contentBoosts() {
    return this.GET("/me/contents/boost");
  }

  async contentStatus(uuid, params) {
    return this.PATCH(`/me/contents/${uuid}/status`, params);
  }

  async updateContent(uuid, params) {
    return this.PUT(`/me/contents/${uuid}`, params);
  }

  async putRevision(uuid, params) {
    return this.PUT(`/me/contents/${uuid}/revision`, params);
  }

  async getRevision(uuid) {
    return this.GET(`/me/contents/${uuid}/revision`);
  }

  async getContentRevisions(uuid) {
    return this.GET(`/me/contents/${uuid}/revision/top`);
  }

  async deleteRevision(uuid) {
    return this.DELETE(`/me/contents/${uuid}/revision`);
  }

  async deleteSerie(uuid) {
    return this.DELETE(`/series/${uuid}`);
  }

  async uploadAvatar(params) {
    return this.POST(`/me/profile/avatar`, params, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
  }

  async deleteAvatar() {
    return this.DELETE("/me/profile/avatar");
  }

  async getMySeries(uuid) {
    return this.GET(`/me/series/${uuid}`);
  }

  async getSeries() {
    return this.GET(`/me/series/top`);
  }

  async getSerie(uuid) {
    return this.GET(`/me/series/${uuid}`);
  }

  async getSeasons(params) {
    return this.GET(
      `/stats/dashboard/creator/${params.uuid}`,
      {
        from: params.from,
        to: params.to,
      },
      {
        headers: {
          "Cache-Control": "no-cache",
        },
      }
    );
  }

  async getChapters(params) {
    return this.GET(
      `/stats/dashboard/creator/season/${params.uuid}`,
      {
        from: params.from,
        to: params.to,
      },
      {
        headers: {
          "Cache-Control": "no-cache",
        },
      }
    );
  }
}
