import ApiClient from "./apiclient";

export default class PaymentService extends ApiClient {
  async content(params) {
    return this.POST(`/payments/content`, params);
  }

  async boost(params) {
    return this.POST(`/payments/content/boost`, params);
  }

  async applyPayTicket(ticket) {
    if (Buffer.isBuffer(ticket)) {
      ticket = ticket.toString("base64");
    }
    return this.POST("/payments/payticket", { ticket });
  }
}
