import ApiClient from "./apiclient";

export default class FollowService extends ApiClient {
  async follow(userId) {
    return this.POST(`/follow/${userId}`);
  }

  async unfollow(userId) {
    return this.DELETE(`/follow/${userId}`);
  }

  async getFollowers() {
    return this.GET('/me/followers');
  }

  async getFolloweds() {
    return this.GET('/me/followeds');
  }
}

