import React, { useState } from "react";
import styles from "./SideNav.module.scss";
import { ReactComponent as Close } from "../../../assets/close.svg";
import { logout } from "../../../store/actions/auth";
import { connect } from "react-redux";
import { motion } from "framer-motion";
import { GAHandler } from "../../../utils/GA";
const container = {
  hidden: {
    opacity: 0,
    transition: {
      when: "afterChildren",
    },
  },
  show: {
    opacity: 1,
    transition: {
      when: "beforeChildren",
      staggerChildren: 0.15,
    },
  },
};

const item = {
  hidden: { opacity: 0 },
  show: { opacity: 1 },
};

const SideNav = ({ showSideNav, showSideNavHandler, logout, showAlertModal, isLoggedIn }) => {
  return (
    <div className={`${styles.sideNav} ${showSideNav ? styles.show : ""}`}>
      {/* <Close className={styles.close} onClick={() => showSideNavHandler(!showSideNav)} /> */}
      <motion.div className={styles.sideNavOptions} variants={showSideNav ? container : null} initial='hidden' animate={showSideNav ? "show" : "hidden"}>
        {isLoggedIn ? (
          <>
            <motion.div
              variants={item}
              className={styles.sideNavOption}
              onClick={() => {
                localStorage.clear("persist:root");
                window.location.reload();
              }}
            >
              <span></span> CERRAR SESIÓN
            </motion.div>
            <motion.div variants={item} className={styles.sideNavOption}>
              <span></span> BORRAR CACHE
            </motion.div>
            <motion.div
              variants={item}
              className={styles.sideNavOption}
              onClick={() => {
                window.open("https://flixxo.com/terminos-y-condiciones/", "_blank");
              }}
            >
              <span></span> POLÍTICA Y PRIVACIDAD
            </motion.div>
            <motion.div
              variants={item}
              className={styles.sideNavOption}
              onClick={() => {
                window.open("https://flixxo.com/terminos-y-condiciones/", "_blank");
              }}
            >
              <span></span> TÉRMINOS Y CONDICIÓNES
            </motion.div>
          </>
        ) : (
          <motion.div
            variants={item}
            className={styles.sideNavOption}
            onClick={() => {
              GAHandler("Despliegue del Pop Up", "PopUp_ClickLogin");
              showSideNavHandler(!showSideNav);
              showAlertModal("signIn");
            }}
          >
            <span></span> INICIAR SESIÓN
          </motion.div>
        )}
      </motion.div>
    </div>
  );
};

export default connect(null, { logout })(SideNav);
