export const checkMedia = (hash) => {
  const secret = store.getState().authReducer.secret;
  const service = new ContentService(secret);

  return async (dispatch) => {
    return dispatch({
      type: types.CONTENT_CHECK_MEDIA,
      payload: service["checkMedia"](hash),
    }).then((res) => {
      return res;
    });
  };
};

export const getByCategory = (params) => {
  const secret = store.getState().authReducer.secret;
  const service = new ContentService(secret);
  let { contentsByCategory, filter } = store.getState().contentReducer;
  let result;
  let toFilter = _.map(contentsByCategory, (cbc, key) => {
    if (key === filter) {
      return cbc;
    } else {
      return undefined;
    }
  });
  let obj = _.filter(toFilter, (tf) => tf !== undefined);
  if (_.isEmpty(contentsByCategory[filter])) {
    result = {
      type: types.CONTENTS_BY_CATEGORY,
      payload: service["getByCategory"](params),
    };
  } else if (obj.length === 0) {
    result = {
      type: types.CONTENTS_BY_CATEGORY,
      payload: service["getByCategory"](params),
    };
  } else if (obj[0][params.id] === undefined) {
    result = {
      type: types.CONTENTS_BY_CATEGORY,
      payload: service["getByCategory"](params),
    };
  } else if (new Date().getTime() - obj[0][params.id].timestamp < tenMinutes) {
    result = {
      type: types.CONTENTS_BY_CATEGORY,
      payload: Promise.resolve(contentsByCategory[filter][params.id].payload),
    };
  } else if (new Date().getTime() - obj[0][params.id].timestamp > tenMinutes) {
    result = {
      type: types.CONTENTS_BY_CATEGORY,
      payload: service["getByCategory"](params),
    };
  }
  return result;
};

