import React, { useState } from "react";
import styles from "./SliderWrapper.module.scss";
import { CarouselProvider, Slider, ButtonBack, ButtonNext } from "pure-react-carousel";
import "pure-react-carousel/dist/react-carousel.es.css";
import { ReactComponent as ArrowLeft } from "../../../assets/arrow-left.svg";
import { ReactComponent as ArrowRight } from "../../../assets/arrow-right.svg";
import { useMediaQuery } from "react-responsive";
import useMediaValues from "../../../utils/hooks/useMediaValues";

const useMediaConfig = {
  mobile: 2,
  tab: 3,
  tabLand: 5,
  desktop: 6,
  bigDesktop: 6,
  defaultValue: 6,
};

const SliderWrapper = ({ children, videos, length }) => {
  const isMobile = useMediaQuery({ query: "(max-width: 600px)" });

  const visibleSlide = useMediaValues(useMediaConfig);
  let slideSize = {
    width: isMobile ? 224 : 234,
    height: isMobile ? 94 : 122,
  };

  const [showOptions, showOptionsHandler] = useState(false);
  return (
    <div
      className={styles.SliderWrapper}
      onPointerEnter={() => showOptionsHandler(!showOptions)}
      onPointerLeave={() => showOptionsHandler(!showOptions)}
    >
      <CarouselProvider
        naturalSlideWidth={slideSize.width}
        naturalSlideHeight={slideSize.height}
        totalSlides={length}
        step={5}
        visibleSlides={visibleSlide}
        dragEnabled={isMobile}
        dragStep={isMobile}
        infinite={true}
        lockOnWindowScroll={true}
        isIntrinsicHeight={true}
      >
        <Slider className={styles.slider}>{children}</Slider>
        {showOptions ? (
          <>
            <ButtonBack className={`${styles.button} ${styles.back}`}>
              <ArrowLeft />
            </ButtonBack>
            <ButtonNext className={`${styles.button} ${styles.next}`} disabled={length < 6}>
              <ArrowRight style={{ opacity: length < 6 ? 0.3 : 1 }} />
            </ButtonNext>{" "}
          </>
        ) : null}
      </CarouselProvider>
    </div>
  );
};

export default SliderWrapper;
