import React, { useEffect } from "react";
import ReactDOM from "react-dom";
import App from "./App";
import "./index.scss";
import PackageInfo from "../package.json";
import { BrowserRouter } from "react-router-dom";
import { Provider } from "react-redux";
import { store, persistor } from "./store/storage/store";
import { PersistGate } from "redux-persist/integration/react";
import ReactGA from "react-ga";
import TagManager from "react-gtm-module";
import environment from "environment";
import scriptjs from "scriptjs";
import Cast from "./utils/cast";
// if (environment.ENV === "production") console.log = () => {};

export const IndexApp = () => {
  useEffect(() => {
    if (environment.ENV === "production") {
      ReactGA.initialize(environment.GA_ID);
      TagManager.initialize({
        gtmId: environment.GTM_ID,
      });
      document.addEventListener("contextmenu", function(event) {
        event.preventDefault();
      });
    }
    // localStorage.removeItem("persist:root");
    setTimeout(() => persistor.purge(), 200);
  }, []);
  return (
    <Provider store={store}>
      <PersistGate persistor={persistor} loading={null}>
        <BrowserRouter>
          <App />
        </BrowserRouter>
      </PersistGate>
    </Provider>
  );
};

scriptjs.get(
  "https://appleid.cdn-apple.com/appleauth/static/jsapi/appleid/1/en_US/appleid.auth.js",
  () => {
    const params = {
      clientId: "com.flixxo.play",
      scope: "name email",
      redirectURI: "http://play.stg.flixxo.com/redirect",
      usePopup: true,
    };
    window.AppleID.auth.init(params);
  }
);

scriptjs.get("https://www.gstatic.com/cv/js/sender/v1/cast_sender.js", () => {
  console.log("WINDOW", window.chrome);
  Cast.init();
});

ReactDOM.render(<IndexApp />, document.getElementById("root"));
