import React, { useState } from "react";
import { connect } from "react-redux";
import { forgotPassword, resetPassword } from "../../../../store/actions/auth";
import Button from "../../../UI/ButtonForm/ButtonForm";
import { GAHandler } from "../../../../utils/GA";
import { COUNTRY_CODES } from "../../../../utils/countryCodes";
import SelectCountry from "../../../UI/SelectCountry/SelectCountry";
import ResendCode from "../../../UI/ResendCode/ResendCode";
const Recover = ({
  styles,
  setTypeContent,
  isLoging,
  forgotPassword,
  resetPassword,
  forgotCodePending,
  forgotCodeError,
  resetPasswordPending,
  resetPasswordError,
}) => {
  let initialCountryCode = COUNTRY_CODES.find((item) => item.value === "+54");
  const [mobile, setMobile] = useState("");
  const [code, setCode] = useState("");
  const [currentCountryCode, setCurrentCountryCode] = useState(initialCountryCode);
  const [password, setPassword] = useState({
    password: "",
    passwordRepeat: "",
  });
  const [recoverStep, setRecoverStep] = useState(1);
  const [inputType, setinputType] = useState("password");
  const [inputType2, setinputType2] = useState("password");

  const sendRecoverHandler = (redirect) => {
    let sendNumber = {
      mobileNumber: `${currentCountryCode.value}${mobile}`,
    };
    forgotPassword(sendNumber, "sms").then(() => {
      if (redirect) setRecoverStep(2);
    });
  };

  const sendPassHandler = () => {
    let sendInfo = {
      mobileNumber: `${currentCountryCode.value}${mobile}`,
      code: code,
      password: password.password,
    };
    resetPassword(sendInfo).then((res) => {
      GAHandler("Recuperar Contraseña", "RecuperarContraseña_Exitosa");
      setTypeContent("signIn");
    });
  };
  const recoverContent = () => {
    if (recoverStep === 1) {
      return (
        <>
          <SelectCountry
            options={COUNTRY_CODES}
            currentCountryCode={currentCountryCode}
            setCurrentCountryCode={setCurrentCountryCode}
          />
          <div className={styles.modalAlert__recoverInputContainer}>
            <input
              value={currentCountryCode.value}
              type='text'
              className={`${styles.modalAlert__input} ${styles.modalAlert__inputCountryCode}`}
            />
            <input
              value={mobile}
              type='text'
              placeholder='Ingresa tu numero de telefono'
              name='phone'
              id='phone'
              className={`${styles.modalAlert__input} ${
                forgotCodeError ? styles.errorBorder : ""
              } ${styles.modalAlert__inputRecover}`}
              onChange={(e) => setMobile(e.target.value)}
            />
          </div>
          {forgotCodeError ? <p className={styles.error}>{forgotCodeError.message}</p> : null}
          <Button
            isLoading={forgotCodePending}
            customStyle={styles.sign}
            onClick={() => sendRecoverHandler(true)}
            hasHover
            hasPlay
          >
            OBTENER CODIGO
          </Button>
        </>
      );
    } else if (recoverStep === 2) {
      return (
        <>
          <div
            className={`${styles.modalAlert__inputContainer} ${styles.modalAlert__inputContainer__code}`}
          >
            <input
              value={code}
              type='text'
              placeholder='Ingresa aqui el Codigo'
              name='verifyCode'
              id='verifyCode'
              className={styles.modalAlert__input}
              onChange={(e) => setCode(e.target.value)}
              autoComplete='off'
            />
          </div>
          <div
            className={`${styles.modalAlert__inputContainer} ${styles.modalAlert__inputContainer__password} ${styles.m20}`}
          >
            <input
              value={password.password}
              type={inputType}
              placeholder='Nueva contraseña'
              name='password'
              id='password'
              className={styles.modalAlert__input}
              onChange={(e) => setPassword({ ...password, password: e.target.value })}
            />
            <figure
              className={`${styles.modalAlert__inputEye} ${
                inputType === "text" ? styles.hide : ""
              }`}
              onClick={() => setinputType(inputType === "password" ? "text" : "password")}
            />
          </div>
          <div
            className={`${styles.modalAlert__inputContainer} ${styles.modalAlert__inputContainer__password}`}
          >
            <input
              value={password.passwordRepeat}
              type={inputType}
              placeholder='Confirmá tu contraseña'
              name='passwordConfirm'
              id='passwordConfirm'
              className={styles.modalAlert__input}
              onChange={(e) => setPassword({ ...password, passwordRepeat: e.target.value })}
            />
            <figure
              className={`${styles.modalAlert__inputEye} ${
                inputType2 === "text" ? styles.hide : ""
              }`}
              onClick={() => setinputType2(inputType2 === "password" ? "text" : "password")}
            />
          </div>
          <ResendCode onClick={sendRecoverHandler} />

          {resetPasswordError ? <p className={styles.error}>{resetPasswordError.message}</p> : null}

          <Button
            isLoading={resetPasswordPending}
            customStyle={styles.sign}
            onClick={sendPassHandler}
          >
            INGRESAR
          </Button>
        </>
      );
    }
  };

  return recoverContent();
};

const mapStateToProps = (state) => {
  return {
    isLoging: state.authReducer.isLoging,
    forgotCodePending: state.authReducer.forgotCodePending,
    forgotCodeError: state.authReducer.forgotCodeError,
    resetPasswordPending: state.authReducer.resetPasswordPending,
    resetPasswordError: state.authReducer.resetPasswordError,
  };
};

export default connect(mapStateToProps, { forgotPassword, resetPassword })(Recover);
