import UserService from "../services/user";
import * as types from "../actions/ActionTypes";
import { store } from "../storage/store";

export const setMobileNumber = (params) => {
  const secret = store.getState().authReducer.secret;
  const service = new UserService(secret);

  return {
    type: types.PUT_MOBILE_NUMBER,
    payload: service["setMobileNumber"](params),
  };
};

