export const balance = () => {
  const secret = store.getState().authReducer.secret;
  const service = new UserService(secret);

  return {
    type: types.USER_BALANCE,
    payload: service["balance"]().then((res) => {
      if (!res.amount) {
        throw res;
      }
      return res;
    }),
  };
};

