import React, { useState } from "react";
import Step1 from "./Step1";
import Step2 from "./Step2";
import { showAlertModal, register, checkRegister, setPreviousAlertModal, authCheck } from "../../../../store/actions/auth";
import { connect } from "react-redux";
import { COUNTRY_CODES } from "../../../../utils/countryCodes";
import GoBack from "../../../UI/GoBack/GoBack";
import { modalRedirect } from "../../../../utils/constants";
const RegisterForm = ({ showAlertModal, register, checkRegister, isRegistering, setPreviousAlertModal, authCheck }) => {
  const [step, setStep] = useState(0);
  const [errorFromAPI, setErrorFromApi] = useState(null);
  const [formData, setFormData] = useState({
    nickname: "",
    email: "",
  });

  let initialCountryCode = COUNTRY_CODES.find((item) => item.value === "+54");
  const [mobile, setMobile] = useState("");
  const [code, setCode] = useState("");
  const [currentCountryCode, setCurrentCountryCode] = useState(initialCountryCode);

  const registerHandler = (phone) => {
    const registerData = {
      email: formData.email,
      password: formData.password,
      nickname: formData.nickname,
      mobileNumber: `${currentCountryCode.value}${phone}`,
    };

    register(registerData)
      .then((res) => {
        showAlertModal("validate");
      })
      .catch((error) => {
        let errorFrom = error.error.details[0];
        if (errorFrom.field === "email" || errorFrom.field === "nickname") {
          setErrorFromApi({ type: errorFrom.field, message: errorFrom.message });
          setStep(0);
        } else if (errorFrom.field === "mobileNumber") {
          setErrorFromApi({ type: errorFrom.field, message: errorFrom.message });
        }
      });

    // showAlertModal("validate");
  };

  const prevHandler = () => {
    if (step === 0) {
      setPreviousAlertModal(modalRedirect.playAsAnon);
    } else {
      setStep(0);
    }
  };

  return (
    <>
      <GoBack fromModal fromModalHandler={prevHandler} />
      {step === 0 && <Step1 setStep={setStep} authCheck={authCheck} setFormData={setFormData} formData={formData} errorFromAPI={errorFromAPI} setErrorFromApi={setErrorFromApi} />}
      {step === 1 && (
        <Step2
          mobile={mobile}
          options={COUNTRY_CODES}
          setMobile={setMobile}
          code={code}
          registerHandler={registerHandler}
          setCode={setCode}
          currentCountryCode={currentCountryCode}
          setCurrentCountryCode={setCurrentCountryCode}
          errorFromAPI={errorFromAPI}
          isRegistering={isRegistering}
        />
      )}
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    isRegistering: state.authReducer.isRegistering,
  };
};

export default connect(mapStateToProps, { showAlertModal, register, checkRegister, setPreviousAlertModal, authCheck })(RegisterForm);
