import * as types from "../actions/ActionTypes";
import UnauthCategorieService from "../services/categorieUnauth";
import { store } from "../../store/storage/store";


export const query = () => {
    const secret = store.getState().authReducer.secret;
    const service = new UnauthCategorieService(secret);

    return {
        type: types.CATEGORIES,
        payload: service["query"]()
    };
};

export const get = params => {
    const secret = store.getState().authReducer.secret;
    const service = new UnauthCategorieService(secret);

    return {
        type: types.CATEGORIES,
        payload: service["get"](params)
    };
};
