export const Decimal18 = Decimal.clone({
  precision: 18,
  rounding: 3,
});

export const flixxos = (price, fixed = 2, places = 4) => {
  const value = new Decimal18(price);
  return value.toDecimalPlaces(places).toFixed(fixed);
};

