import React, { useEffect, useRef, useState } from "react";
import { useAnimation } from "framer-motion";
import UserBalance from "../UserBalance/UserBalance";
import Avatar from "../Avatar/Avatar";
import { parseBalance } from "../../../utils/common";
import PropTypes from "prop-types";

const UserInfo = ({ avatar, balance, isSearching }) => {
  const controls = useAnimation();
  let { amount, prevAmount } = parseBalance(balance);
  const [isStopped, setIsStopped] = useState(true);
  const isInitialMount = useRef(true);

  let eventListenerCoin = [
    {
      eventName: "complete",
      callback: () => {
        setIsStopped(true);
      },
    },
  ];

  useEffect(() => {
    if (isInitialMount.current) {
      isInitialMount.current = false;
    } else {
      controls.start({
        scale: 1,
        transition: {
          type: "spring",
          velocity: 50,
          stiffness: 700,
          damping: 80,
        },
      });
      setIsStopped(false);
    }
  }, [amount]);

  return (
    <>
      <UserBalance controls={controls} eventListenerCoin={eventListenerCoin} prevAmount={prevAmount} amount={amount} isStopped={isStopped} />
      <Avatar image={avatar} />
    </>
  );
};

UserInfo.propTypes = {
  balance: PropTypes.shape({
    amount: PropTypes.number,
    prevAmount: PropTypes.number,
  }),
  avatar: PropTypes.string,
};

export default UserInfo;
