import { UnauthApiClient } from "./apiclient";
import { store } from "../storage/store";

export default class AuthService extends UnauthApiClient {
  login(fields) {
    return this.POST("/auth/login", fields);
  }

  loginExternal(provider, fields) {
    return this.POST(`/auth/loginExternal/${provider}`, fields);
  }

  register(fields) {
    return this.POST("/auth/register", fields);
  }

  check(fields) {
    return this.POST("/auth/check", fields);
  }

  confirm(fields) {
    return this.POST("/auth/confirm/sms", fields);
  }

  forgotCode(mobileNumber, method) {
    return this.POST(`/auth/forgotcode/${method}`, mobileNumber);
  }

  resetPassword(params) {
    return this.POST("/auth/resetpassword", params);
  }

  async resendCode(params) {
    return this.POST("/auth/resendcode/sms", params);
  }

  async changepassword(fields) {
    const cipher = await store.getState().authReducer.cipher;
    const ciphered = await cipher.encode(fields);
    const token = await this.genToken("/auth/changepassword");

    return this.POST("/auth/changepassword", ciphered, {
      headers: {
        "Content-Type": "text/plain",
        Authorization: token,
      },
    });
  }

  async genToken(path) {
    const signer = await store.getState().authReducer.signer;
    const authToken = await signer.generateToken(path);
    return `Basic ${btoa(authToken)}`;
  }

  async authCheck(body) {
    return this.POST(`/auth/check`, body);
  }
}
