import FollowService from "../services/follow";
import * as types from "../actions/ActionTypes";
import { store } from "../storage/store";

export const getFollowers = () => {
  const secret = store.getState().authReducer.secret;
  const service = new FollowService(secret);

  return {
    type: types.FOLLOWERS,
    payload: service["getFollowers"](),
  };
};

export const getFolloweds = () => {
  const secret = store.getState().authReducer.secret;
  const service = new FollowService(secret);

  return {
    type: types.FOLLOWEDS,
    payload: service["getFolloweds"](),
  };
};

export const follow = (params) => {
  const secret = store.getState().authReducer.secret;
  const service = new FollowService(secret);

  return (dispatch) => {
    return dispatch({
      type: types.FOLLOW,
      payload: service["follow"](params),
    }).then(() => dispatch(getFolloweds()));
  };
};

export const unfollow = (params) => {
  const secret = store.getState().authReducer.secret;
  const service = new FollowService(secret);

  return (dispatch) => {
    return dispatch({
      type: types.UNFOLLOW,
      payload: service["unfollow"](params),
    }).then(() => dispatch(getFolloweds()));
  };
};

export const clearUnfollowError = () => {
  return {
    type: types.CLEAR_UNFOLLOW_ERROR,
  };
};

export const clearFollowError = () => {
  return {
    type: types.CLEAR_FOLLOW_ERROR,
  };
};
