import React, { useState } from "react";
import styles from "./SwitchSeason.module.scss";
import { ReactComponent as ArrowBottom } from "../../../assets/arrow-bottom.svg";
import PropTypes from "prop-types";
const SwitchSeason = ({ season, currentSeason, setCurrentSeason, desktop }) => {
  const [showOptions, showOptionsHandler] = useState(false);
  let seasonNumber = currentSeason + 1;

  if (seasonNumber < 10) {
    seasonNumber = `0${seasonNumber}`;
  }

  const formatNumber = (number) => {
    if (number < 10) {
      return `0${number}`;
    } else {
      return number;
    }
  };

  const changeSeasonHandler = (index) => {
    setCurrentSeason(index);
  };

  return (
    <div
      onClick={() => showOptionsHandler(!showOptions)}
      className={`${styles.switchSeason} ${showOptions ? styles.isShowing : ""} ${
        desktop ? styles.desktop : styles.mobile
      }`}
    >
      <div className={styles.switchSeasonCurrent}>
        Temporada-{season[currentSeason] && formatNumber(season[currentSeason].number)}
      </div>
      <ArrowBottom className={styles.switchSeasonArrow} />
      {showOptions ? (
        <div className={styles.switchSeasonOptions}>
          {season.map((option, index) => {
            return (
              <div
                className={styles.switchSeasonOptionsItem}
                onClick={() => changeSeasonHandler(index)}
              >
                Temporada {formatNumber(option.number)}
              </div>
            );
          })}
        </div>
      ) : null}
    </div>
  );
};

SwitchSeason.propTypes = {
  season: PropTypes.array,
  currentSeason: PropTypes.number,
  setCurrentSeason: PropTypes.func,
  desktop: PropTypes.bool,
};

export default SwitchSeason;
