import { applyMiddleware, createStore, compose } from "redux";
import promise from "redux-promise-middleware";
import thunk from "redux-thunk";

import { persistStore, persistReducer, createTransform } from "redux-persist";
import storage from "./storage";
import rootReducer from "../reducers/rootReducer";
import { createFilter, createBlacklistFilter } from "redux-persist-transform-filter";

import { Signer, Cipher } from "com.flixxo.lib.authorizator";
let initialState = {};
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const authFilter = createFilter("authReducer", ["secret", "isModerator", "seriesCreator", "email", "isLoggedIn", "isRegistered", "isConfirmed"]);
// const authFilter = createFilter("authReducer", ["showAlertModal", "showAlertModalType"]);
const userFilter = createFilter("userReducer", ["seed", "user"]);
const torrentFilter = createFilter("torrentReducer", ["torrents"]);
const onboardingFilter = createFilter("onboardingReducer", ["open"]);
const contentFilter = createFilter("contentReducer", ["contentsByCategory", "contentsByUser", "contentsByIds"]);
const categoriesFilter = createFilter("categorieReducer", ["categories"]);
const signerAndCipherTransform = createTransform(
  (inboundState) => inboundState,
  (outboundState, key) => {
    if (key === "authReducer") {
      if (outboundState.secret) {
        return {
          ...outboundState,
          signer: new Signer(outboundState.secret),
          cipher: new Cipher(outboundState.secret),
        };
      } else return outboundState;
    }
    return outboundState;
  }
);

const persistConfig = {
  key: "root",
  transforms: [
    authFilter,
    // userFilter,
    // torrentFilter,
    // onboardingFilter,
    // contentFilter,
    // categoriesFilter,
    signerAndCipherTransform,
  ],
  whitelist: ["authReducer", "userReducer", "torrentReducer", "onboardingReducer", "contentReducer", "categorieReducer"],
  storage,
};

const persistedReducer = persistReducer(persistConfig, rootReducer);

export let store = createStore(persistedReducer, initialState, composeEnhancers(applyMiddleware(thunk, promise)));

export let storeStorybook = createStore(rootReducer, composeEnhancers(applyMiddleware(thunk)));

export let persistor = persistStore(store);
