import { combineReducers } from "redux";
import AuthReducer from "./auth";
import OnboardingReducer from "./onboarding";
import CategorieReducer from "./categorie";
import UserReducer from "./user";
import ContentReducer from "./content";
import SliderReducer from "./slider";
import AdReducer from "./ad";
import TorrentReducer from "./torrent";
import MediaReducer from "./media";
import PaymentReducer from "./payment";
import SearchReducer from "./search";
import FollowReducer from "./follow";
import MailReducer from "./mail";
import AppReducer from "./app";
import SerieReducer from "./serie";
import GAReducer from "./ga";
import CarruselReducer from "./carrusels";

export default combineReducers({
  authReducer: AuthReducer,
  onboardingReducer: OnboardingReducer,
  categorieReducer: CategorieReducer,
  userReducer: UserReducer,
  contentReducer: ContentReducer,
  sliderReducer: SliderReducer,
  adReducer: AdReducer,
  torrentReducer: TorrentReducer,
  mediaReducer: MediaReducer,
  paymentReducer: PaymentReducer,
  searchReducer: SearchReducer,
  followReducer: FollowReducer,
  mailReducer: MailReducer,
  appReducer: AppReducer,
  serieReducer: SerieReducer,
  gaReducer: GAReducer,
  carruselsReducer: CarruselReducer,
});
