import React from "react";
import styles from "./SerieSlider.module.scss";
// import "./sliderConfig.scss";
import SerieSliderWrapper from "./SerieSliderWrapper/SerieSliderWrapper";
import SerieSliderItem from "./SerieSliderItem.js/SerieSliderItem";

const SerieSlider = ({ content }) => {
  return (
    <div className={styles.sliderContainer}>
      <SerieSliderWrapper length={content.length}>
        {content.map((video, index) => {
          return <SerieSliderItem video={video} index={index} />;
        })}
      </SerieSliderWrapper>
    </div>
  );
};

export default SerieSlider;
