import React, { useEffect, useState } from "react";
import styles from "./ContentInfo.module.scss";
import { connect } from "react-redux";
import ButtonPurchase from "../ButtonPurchase/ButtonPurchase";
import { ReactComponent as FlixxoCoin } from "../../../assets/flixxo-coin.svg";
import DurationAuthorInfo from "../DurationAuthorInfo/DurationAuthorInfo";
import { getWebLink, setContentWeblinkURL } from "../../../store/actions/content";
import { content as buyContent } from "../../../store/actions/payment";
import { getHero } from "../../../utils/thumbsManager";
import { flixxos } from "../../../utils/constants";
import "pure-react-carousel/dist/react-carousel.es.css";
import { showAlertModal } from "../../../store/actions/auth";
import { setDismissNextEpisode } from "../../../store/actions/media";
import { balance as userBalance } from "../../../store/actions/user";
import { useMediaQuery } from "react-responsive";
import Button from "../../UI/ButtonForm/ButtonForm";
import SeasonSwitch from "../../UI/SwitchSeason/SwitchSeason";
import useButtonHandler from "../../../utils/hooks/UseButtonHandler";
import { canPurchase, findInPurchased, parseSubtitles } from "../../../utils/common";
import ContentActions from "../ContentActions/ContentActions";
import { useHistory } from "react-router-dom";
import CastButton from "../../UI/CastButton/CastButton";
const ContentInfo = ({
  currentSeason,
  setCurrentSeason,
  video,
  allCategories,
  setDismissNextEpisode,
  isLoggedIn,
  currentSerieLanding,
  fromNextEpisode,
  contentPurchasedArray,
  fromLanding,
  fromHome,
  balance,
  isEpisode,
  paymentContentPosting,
  nextEpisodeNumber,
  communityFilter,
}) => {
  const isMobile = useMediaQuery({ query: "(max-width: 600px)" });
  const [animateContent, SetAnimateContent] = useState(true);
  const history = useHistory();
  useEffect(() => {
    if (fromLanding && !isMobile) {
      SetAnimateContent(true);
      setTimeout(() => {
        SetAnimateContent(false);
      }, 800);
    }
  }, [video.body]);

  const canPurchased = canPurchase(balance.amount, video.price);
  let isPurchased = findInPurchased(contentPurchasedArray, video);
  const contentType = video.contentType === 1 ? "Comunidad" : "Serie";
  const mainThumbUrl = getHero(video);
  const { title } = currentSerieLanding ? currentSerieLanding : video;
  const { body } = video || video.metadata[0];
  const lang = video.audioLang || "";
  const category = allCategories.find((cat) => cat.id === video.categoryId);
  const relaseYear = video.releaseDate.split("-")[0];
  let minutes = Math.floor(video.duration / 60);
  const episodeCount = isEpisode && video.noEpisodeSelected ? video.season[0].content.length : "";
  const subtitlesCount = parseSubtitles(video.subtitle);

  const [buttonHandler] = useButtonHandler(
    isPurchased,
    canPurchased,
    fromHome,
    fromNextEpisode,
    video,
    contentType
  );

  const secondaryHandler = () => {
    if (fromHome) history.push(`/community/${video.uuid}`);
    else if (fromNextEpisode) setDismissNextEpisode();
  };

  const author = isEpisode && !video.noEpisodeSelected ? currentSerieLanding.author : video.author;
  const limitBody = fromNextEpisode ? 120 : 235;
  const bodyLimited = body.slice(0, limitBody);

  let { episodeNumber } = video;

  return (
    <div
      className={`${styles.contentInfoContainer} ${
        animateContent && fromLanding ? styles.animation : ""
      }`}
    >
      {isMobile && !fromLanding ? null : (
        <div
          className={`${styles.contentInfo_info} ${fromLanding ? styles.fromLanding : ""}  ${
            fromNextEpisode ? styles.fromNextEpisode : ""
          }`}
        >
          <h2 className={`${title.length >= 18 ? styles.minTitle : ""}`}>{title}</h2>
          {isEpisode && !video.noEpisodeSelected ? (
            <h3 className={styles.episode}>
              {`Episodio ${fromNextEpisode ? nextEpisodeNumber : episodeNumber}\xa0 |   \xa0`}
              <span>{`${video.title}`}</span>
            </h3>
          ) : (
            <h3>{`${contentType === "Serie" ? "" : relaseYear} ${
              contentType === "Serie" ? "" : "\xa0  | \xa0"
            } ${
              fromLanding ? video.category?.name : video.category?.name || category?.name
            }  \xa0 ${contentType === "Comunidad" ? `| \xa0 ${minutes}:00min \xa0` : ""} ${
              contentType === "Serie" ? `|  \xa0 ${episodeCount} Episodios \xa0  ` : ""
            } | \xa0 Audio (${lang}) \xa0 ${subtitlesCount &&
              `| \xa0 Subtitulos (${subtitlesCount})`} `}</h3>
            // ${minutes} min
          )}

          {fromNextEpisode || isMobile ? null : (
            <DurationAuthorInfo
              author={author}
              minutes={minutes}
              episodeSelected={!video.noEpisodeSelected}
              isEpisode={isEpisode}
              contentType={contentType}
            />
          )}

          {!isMobile && (
            <p className={styles.description}>{`${bodyLimited}${
              body.length > limitBody ? "..." : ""
            }`}</p>
          )}
        </div>
      )}

      {isMobile ? (
        <Button hasHover hasPlay onClick={buttonHandler} customStyle={styles.mobileButton}>
          VER AHORA
        </Button>
      ) : (
        <div className={`${styles.contentInfo_container} ${fromHome && styles.fromHome}`}>
          <ContentActions
            contentType={fromHome && !fromNextEpisode ? "community" : ""}
            canPurchase={canPurchased}
            isPurchased={isPurchased}
            isLoggedIn={isLoggedIn}
            price={video.price}
            episodeNumber={video.episodeNumber}
            primaryButtonHandler={buttonHandler}
            secondaryButtonHandler={secondaryHandler}
            fromNextEpisode={fromNextEpisode}
          />
          {isPurchased && fromLanding && <CastButton uuid={video.uuid} />}
        </div>
      )}

      {isMobile && fromLanding && !fromNextEpisode ? (
        <DurationAuthorInfo
          author={author}
          minutes={minutes}
          episodeSelected={!video.noEpisodeSelected}
          isEpisode={isEpisode}
          contentType={contentType}
        />
      ) : null}
      {isMobile && fromLanding && !fromNextEpisode ? (
        <p className={styles.description}>{bodyLimited}</p>
      ) : null}
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    allCategories: state.categorieReducer.categories,
    isLoggedIn: state.authReducer.isLoggedIn,
    balance: state.userReducer.balance,
    communityFilter: state.userReducer.communityFilter,
    currentSerieLanding: state.contentReducer.currentSerieLanding,
    contentPurchasedArray: state.userReducer.contentPurchased,
    paymentContentPosting: state.paymentReducer.paymentContentPosting,
  };
};

export default connect(mapStateToProps, {
  showAlertModal,
  getWebLink,
  setDismissNextEpisode,
  setContentWeblinkURL,
  buyContent,
  userBalance,
})(ContentInfo);
