import React from "react";
import styles from "./Search.module.scss";
import { connect } from "react-redux";
import SliderItem from "../../components/Slider/SliderItem/SliderItem";
import SliderSkeleton from "../../components/UI/SliderSkeleton/SliderSkeleton";
import { ReactComponent as SearchSVG } from "../../assets/search-flixxo.svg";
const Search = ({ searchContent, searchText }) => {
  let { contents, count } = searchContent;

  return (
    <main className={styles.search}>
      {searchText ? (
        <>
          {contents ? (
            <h1>
              Tus resultados para: <span> {searchText}</span>
            </h1>
          ) : (
            <div style={{ width: "100%", marginBottom: "3rem" }}></div>
          )}
          <div className={styles.searchContainer}>
            {!contents ? <SliderSkeleton /> : null}
            {contents &&
              contents.map((cont) => {
                return <SliderItem video={cont} fromSearch={true} />;
              })}
          </div>
        </>
      ) : (
        <div className={styles.searchInitial}>
          <SearchSVG />
          <h1>¿Qué quieres ver ahora?</h1>
          <h2>Buscá por usuarios, nombre de video y más.</h2>
        </div>
      )}
    </main>
  );
};

const mapStateToProps = (state) => {
  return {
    searchContent: state.searchReducer.searchResults,
    searchText: state.searchReducer.searchText,
  };
};
export default connect(mapStateToProps)(Search);
