import { UnauthApiClient } from "./apiclient";

export default class CarruselsService extends UnauthApiClient {
  // async get() {
  //   return this.GET("/carrusels");
  // }

  async byId(uuid) {
    return this.GET(`/carrusels/${uuid}`);
  }

  getCarruselsContent = async (uuid, hasBody) => {
    return this.GET(`/carrusels/${uuid}/contents?hasBody=${hasBody}`);
  };

  getCarruselsSeries = async (uuid, hasBody) => {
    return this.GET(`/carrusels/${uuid}/series?hasBody=${hasBody}`);
  };

  get = async () => {
    return this.GET("/carrusels");
  };
}
