import axios from "axios";
class AxiosApiClient {
  constructor(baseUrl = "", { headers = {} } = {}) {
    if (!baseUrl) throw new Error("missing baseUrl");
    this.headers = {
      Accept: "application/json",
      "Content-Type": "application/json",
    };
    Object.assign(this.headers, headers);
    this.baseUrl = baseUrl;
  }

  fullRoute(url) {
    return `${this.baseUrl}${url}`;
  }

  fetch(route, method, body, isQuery = false, headers = {}) {
    if (!route) throw new Error("Route is undefined");

    let fullRoute = this.fullRoute(route);

    if (isQuery && body) {
      //   var qs = require('qs');
      //   const query = qs.stringify(body);
      const querys = Object.keys(body)
        .map((key) => {
          return `?${key}=${body[key]}`;
        })
        .join();
      fullRoute = `${fullRoute}${querys}`;
      body = undefined;
    }
    let opts = {
      method,
      headers: { ...this.headers, ...headers },
    };
    if (body) {
      Object.assign(opts, { body: JSON.stringify(body) });
    }
    // return axios({ url: fullRoute, ...opts }).then((response) => response.data);
    const fetchPromise = () => fetch(fullRoute, opts);
    // const fetchPromise = () => fetchAxios();

    return fetchPromise().then((response) => {
      if ((response.url.includes("auth/confirm/sms") || response.url.includes("/auth/resendcode/sms") || response.url.includes("/auth/check")) && response.status === 204) {
        return { success: true };
      } else {
        return response.json();
      }
    });
  }

  GET(route, query) {
    return this.fetch(route, "GET", query, true);
  }
  POST(route, body) {
    return this.fetch(route, "POST", body);
  }
  PUT(route, body) {
    return this.fetch(route, "PUT", body);
  }
  DELETE(route, query) {
    return this._fetch(route, "DELETE", query, true);
  }
}

export default AxiosApiClient;
