import React from "react";
import styles from "./ModalSuccess.module.scss";
import Button from "../ButtonForm/ButtonForm";
import { connect } from "react-redux";
import { closeSuccessModal } from "../../../store/actions/auth";
import { ReactComponent as CloseModal } from "../../../assets/close-modal.svg";
import Lottie from "react-lottie";
import logoData from "../../../assets/flixxo-logo.json";
import { useMediaQuery } from "react-responsive";

const defaultOptions = {
  loop: false,
  autoplay: true,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
    viewBoxOnly: true,
    className: styles.modalSuccess_lottie,
  },
};

const ModalSuccess = ({ closeSuccessModal, user: { nickname } }) => {
  const isMobile = useMediaQuery({ query: "(max-width: 600px)" });
  return (
    <>
      <div className={styles.modalSuccessOverlay} onClick={closeSuccessModal} />
      <div className={`${styles.modalSuccess}`}>
        <CloseModal className={styles.closeModalSvg} onClick={closeSuccessModal} />
        <div className={styles.modalSuccess_container}>
          <Lottie options={{ animationData: logoData, ...defaultOptions }} width={isMobile ? 184 : 254} />
          <h2>
            Felicidades @{nickname}, <br /> te has registrado exitosamente
          </h2>
          <h3>
            <span>¡</span>BIENVENIDO A NUESTRA COMUNIDAD<span>!</span>
          </h3>
          <Button onClick={closeSuccessModal} customStyle={styles.modalSuccess_button}>
            COMENZA A DISFRUTAR
          </Button>
        </div>
      </div>
    </>
  );
};
const mapStateToProps = (state) => ({
  user: state.userReducer.user,
});

export default connect(mapStateToProps, { closeSuccessModal })(ModalSuccess);
