import React from "react";
import styles from "./ResendCode.module.scss";

const ResendCode = ({ onClick }) => {
  return (
    <div className={styles.codeContainer}>
      <h5 classNmae={styles.code}>¿No te llego el código?</h5>
      <div className={styles.code__recover} onClick={onClick}>
        <span>VOLVER A ENVIAR</span>
      </div>
    </div>
  );
};

export default ResendCode;
