import React from "react";
import styles from "./SliderSkeleton.module.scss";
import useMediaValues from "../../../utils/hooks/useMediaValues";
const useMediaConfig = {
  mobile: 2,
  tab: 3,
  tabLand: 4,
  desktop: 5,
  bigDesktop: 6,
  defaultValue: 6,
};

const SliderSkeleton = () => {
  const visibleSlide = useMediaValues(useMediaConfig);
  let toMap = Array.from(Array(visibleSlide).keys());

  return (
    <div className={styles.sliderSkeleton}>
      {toMap.map(() => {
        return <div className={styles.sliderSkeletonItem}> </div>;
      })}
    </div>
  );
};

export default SliderSkeleton;
