import { useDispatch, useSelector } from "react-redux";
import { GAHandler } from "../GA";
import axios from "axios";
import { showAlertModal } from "../../store/actions/auth";
import { get as getAd, toggleAdModal } from "../../store/actions/ad";
import {
  setDismissNextEpisode,
  setPlayerEnd,
  resetNextEpisodeValues,
} from "../../store/actions/media";
import { useHistory } from "react-router-dom";
import {
  setCurrentContentForLanding,
  removeContentWeblinkURL,
  setVideoSubtitles,
} from "../../store/actions/content";
import { contentPurchased, balance as userBalance } from "../../store/actions/user";
import { getWebLink, setContentWeblinkURL } from "../../store/actions/content";
import { content as buyContent } from "../../store/actions/payment";
import { COMMUNITY_FILTER } from "../constants";
import React from "react";

export default function UseButtonHandler(
  isPurchased,
  canPurchased,
  fromHome,
  fromNextEpisode,
  video,
  contentType
) {
  const history = useHistory();
  const isLoggedIn = useSelector((state) => state.authReducer.isLoggedIn);
  const communityFilter = useSelector((state) => state.userReducer.communityFilter);
  const currentSerieLanding = useSelector((state) => state.contentReducer.currentSerieLanding);
  const dispatch = useDispatch();

  const GABuyHandler = () => {
    // if (communityFilter === COMMUNITY_FILTER.COMMUNITY)
    //   GAHandler("Comunidad", "Comunidad_CompraExitosa", video.price);
    // else GAHandler("Series", "Series_CompraExitosa", video.price);
  };

  const GAPlayHandler = () => {
    if (communityFilter === COMMUNITY_FILTER.SERIES) {
      // GAHandler("Series", "Series_ReproducciónExitosa", 0);
      // GAHandler("Series", `Series_ReproducciónExitosa_${currentSerieLanding.category.name}`);
    } else {
      // GAHandler("Comunidad", "Comunidad_ReproducciónExitosa", 0);
      // GAHandler("Comunidad", `Comunidad_ReproducciónExitosa_${video.category.name}`);
    }
  };

  const setWeblinkContent = (weblink) => {
    if (video?.subtitle.length) {
      let subtitlesArray = video.subtitle.map(async (sub) => {
        let response = await axios.get(sub.url);
        const trackBlob = new Blob([response.data], {
          type: "text/plain;charset=utf-8",
        });
        const trackUrl = URL.createObjectURL(trackBlob);
        return { kind: "subtitles", src: trackUrl, srcLang: sub.lang, default: sub.lang === "es" };
      });
      Promise.all(subtitlesArray).then((response) => {
        dispatch(setVideoSubtitles(response));
        dispatch(setContentWeblinkURL(weblink));
      });
    } else {
      dispatch(setContentWeblinkURL(weblink));
    }
  };

  const buttonHandler = async () => {
    if (!isLoggedIn) {
      GAHandler("Despliegue del Pop Up", "PopUp_ClickContenido");
      // if (fromHome)
      //   history.push(`/${contentType === "community" ? "community" : "series"}/${video.uuid}`);
      dispatch(showAlertModal("playAsAnon"));
    } else {
      // if (fromHome) {
      //   history.push(`/${contentType === "community" ? "community" : "series"}/${video.uuid}`);
      // } else {
      if (isPurchased) {
        await dispatch(getWebLink(video.uuid)).then((weblink) => {
          if (fromNextEpisode) {
            dispatch(
              setCurrentContentForLanding({
                ...video,
                isPurchased: isPurchased,
              })
            );
            dispatch(setPlayerEnd());
            dispatch(removeContentWeblinkURL());
            dispatch(resetNextEpisodeValues());
          }
          GAPlayHandler();
          GAHandler("Contenido", "Contenido_ReproducciónExitosa");
          setWeblinkContent(weblink.value);
        });
      } else if (canPurchased) {
        await dispatch(
          buyContent({
            contentId: video.uuid,
            price: parseFloat(video.price),
          })
        ).then(async () => {
          GAHandler("Contenido", "Contenido_CompraExitosa", video.price);
          GABuyHandler();
          dispatch(contentPurchased(communityFilter));
          dispatch(userBalance());
          //play
          await dispatch(getWebLink(video.uuid)).then((weblink) => {
            if (fromNextEpisode) {
              dispatch(
                setCurrentContentForLanding({
                  ...video,
                  isPurchased: isPurchased,
                })
              );
              dispatch(setPlayerEnd());
              dispatch(removeContentWeblinkURL());
              dispatch(resetNextEpisodeValues());
            }
            GAPlayHandler();
            GAHandler("Contenido", "Contenido_ReproducciónExitosa");
            setWeblinkContent(weblink.value);
          });
        });
      } else if (!canPurchased) {
        // getAd();
        dispatch(toggleAdModal());
      }
      // }
    }
  };

  return [buttonHandler];
}
