import React, { useEffect } from "react";
import styles from "./CastButton.module.scss";
import { ReactComponent as CastIcon } from "../../../assets/cast-button.svg";
import Cast from "../../../utils/cast";
import { getWebLink } from "../../../store/actions/content";
import { useDispatch } from "react-redux";

const CastButton = ({ uuid }) => {
  const dispatch = useDispatch();
  const handleClick = async () => {
    const response = await dispatch(getWebLink(uuid));
    Cast.launchApp(response.value);
  };

  return (
    <button onClick={handleClick} className={styles.castButton}>
      <CastIcon />
    </button>
  );
};

export default CastButton;
